import React,{Component} from 'react';

function handleClick() {
    var videoPlayer = document.getElementsByTagName('video');
    var audioPlayer = document.getElementsByTagName('audio');
    for(var i = 0; i < videoPlayer.length; i++){
	    videoPlayer[i].pause();
	  }
	  for(var i = 0; i < audioPlayer.length; i++){
	    audioPlayer[i].pause();
	  }
  }

function CoursesTabs({number}){
		return(
			<ul className="nav nav-tabs custom-tabs nowrap courses-tabs" id="myTab" role="tablist">
	          <li className="nav-item"  onClick={handleClick}>
	            <a className="nav-link active" id={'module-info-tab'+number} data-toggle="tab" href={'#module-info'+number} role="tab" aria-controls={'module-info'+number}
	              aria-selected="true">Info</a>
	          </li>
	          {/* <li className="nav-item" onClick={handleClick}>
	            <a className="nav-link" id={'images-tab'+number} data-toggle="tab" href={'#images'+number} role="tab" aria-controls={'images'+number}
	              aria-selected="false">Images</a>
	          </li> */}
	          <li className="nav-item" onClick={handleClick}>
	            <a className="nav-link" id={'videos-tab'+number}  data-toggle="tab" href={'#videos'+number} role="tab" aria-controls={'videos'+number}
	              aria-selected="false">Videos</a>
	          </li>
	          <li className="nav-item" onClick={handleClick}>
	            <a className="nav-link" id={'audio-tab'+number} data-toggle="tab" href={'#audio'+number} role="tab" aria-controls={'audio'+number}
	              aria-selected="false">Audio</a>
	          </li>
	          <li className="nav-item" onClick={handleClick}>
	            <a className="nav-link" id={'transcripts-tab'+number} data-toggle="tab" href={'#transcripts'+number} role="tab" aria-controls={'transcripts'+number}
	              aria-selected="false">Transcripts</a>
	          </li>
	          <li className="nav-item">
	            <a className="nav-link" id={'handout-pdf-tab'+number} data-toggle="tab" href={'#handout-pdf'+number} role="tab" aria-controls={'handout-pdf'+number}
	              aria-selected="false">Handout PDFs</a>
	          </li>
	        </ul>
			);
}

export default CoursesTabs;