import React, { Component } from 'react';
import RoadToDharmaHeader from '../../components/frontend/common/RoadToDharmaHeader';
import DharmaFooter from '../../components/frontend/common/DharmaFooter';
import AOS from 'aos';

class PageNotFound extends Component {

	componentDidMount() {
		AOS.init();
	}
	render() {

		return (
			<>
				<RoadToDharmaHeader />
				<main class="admin-content">
					<section class="sec">
						<div class="container">
							<div class="row">
								<div class="col-xl-12 col-lg-12 col-md-12 col-sm-12">
									<div class="thankyou-div">
										<img src="../../images/error-red.png" class="img-fluid" />
										<h1>404!</h1>
										<h3>PAGE NOT FOUND</h3>
									</div>
								</div>
							</div>
						</div>
					</section>
				</main>
				<DharmaFooter />
			</>
		);
	}
}

export default PageNotFound;