import React,{Component} from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import {fetcCourseAllDetails, fetchRelatedCourses} from '../../actions/courseAction';
import {ToastsContainer, ToastsStore, ToastsContainerPosition} from 'react-toasts';
import { getUserId, apiRoute, getApiHeader } from '../../utils/helpers';
import Header from '../../components/user/Header';
import Banner from '../../components/frontend/common/Banner';
import CourseDetailInfo from '../../components/frontend/course/CourseDetailInfo';
import CourseAllDetails from '../../components/user/CourseAllDetails';

class CourseDetails extends Component{

	constructor(props){
    super(props);
      this.state = {
      	banner : {},
	    } 
	  }


	componentDidMount() {
	    window.scrollTo(0, 0);
	     const requestOptions = {
			headers : getApiHeader()
		};
		axios.get(apiRoute('cms-page-banner/'+btoa(13)),requestOptions)
		.then(res =>{
			this.setState({ banner : res.data });
	    })

	    let userId = getUserId(this.props.history);
		this.props.fetcCourseAllDetails(this.props.match.params.courseId);
		this.props.fetchRelatedCourses(userId,this.props.match.params.courseId);
	}
	render(){
		const courseId = this.props.match.params.courseId;
		const { alert } = this.props;
		return(
			<>
			{alert.message && alert.type == 'success' &&
                  ToastsStore.success(alert.message) }
            {alert && alert.type == 'error' &&
                  ToastsStore.error('Something went wrong') } 
			<ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT}/>
			<Header  history={this.props.history}/>
			  <main className="admin-content">
    			<Banner banner={this.state.banner}/>
    			<section className="sec-cinfo">
      			<div className="container">
      				<CourseDetailInfo courseId={courseId} history={this.props.history}/>
      				<CourseAllDetails isParchased={1} course={this.props.courseDetails} relatedCourses={this.props.relatedCourses} history={this.props.history}/>
      			</div>
      			</section>
			  </main>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  courseDetails: state.courses.allDetails,
  relatedCourses: state.userDetails.relatedCourses,
  alert: state.alert,
});
export default connect(mapStateToProps, {fetcCourseAllDetails, fetchRelatedCourses})(CourseDetails);