import React,{Component} from 'react';
import { Link } from 'react-router-dom';
import { courseImagePath, getLocalStorageAuth} from '../../../utils/helpers';

function CourseListDetails({course}){
  const auth = getLocalStorageAuth();
  let purchasedCoursesId = false;
  let isPurchased = false;
  if(auth){
     purchasedCoursesId = auth.purchased_courses;
     isPurchased = purchasedCoursesId.includes(JSON.stringify(course.id));
  }
	return(
		<div className="col-lg-12 col-md-12 col-sm-12">
            <div className="card card-course">
              <div className="card-course-img">
                <img src={courseImagePath(course.image)} className="img-fluid"/>
              </div>
              <div className="card-course-content">
              <div className="course-heading"><h6>{course.title}</h6>
              <div className="course-rating">
                <div className="price-tag">                                  
                  <span className="course-price"><h6>Course Price</h6> ${course.price}<small> USD</small></span>
                </div>
                {/* <span className="course-price"><h6>Actual Price :</h6>${course.price}<small> USD</small></span> */}
                  
                    {/*<i className="fas fa-star"></i> <i className="fas fa-star"></i> <i className="fas fa-star"></i> <i className="fas fa-star"></i> <i className="far fa-star"></i>*/}
                  {/* <span className="course-duration"><img src="../../images/calendar.svg" />{ course.duration}   {course.duration_type}</span> */}
                </div>
                </div> 
                {/*  <Link to={window.PUBLIC_URL+'/teacher-details/'+btoa(course.teacher_id)}>{course.teacher_name} <i className="fa fa-angle-right" aria-hidden="true"></i></Link>*/}
                <p>{course.description}</p>
                <strong className="text-purple">Course Benefits</strong>
                <ul className="course-benfits">
                  {course.benefits && course.benefits.map((benefit,index) => {
                          return(
                              <li key={index}>{benefit}</li>
                              );
                      })}
                </ul>
                <strong className="text-purple">Course Offerings</strong>
                <ul className="course-offer">
                <ul class="course-offer">
                <li><i class="fas fa-video"></i> Ten Stunning Episodes</li>
                <li><i class="fa fa-book"></i> Ten In-Depth Chapters</li>
                <li><i class="fas fa-volume-up"></i>Ten Audio Meditation Practices</li>
                <li><i class="far fa-comments"></i> TEN DHARMA WISDOM TALKS  (Over 5 Hours)</li>
                </ul>
                {/* {course.offering && course.offering.map((offer,index) => {
                          return(
                              <li key={index}><i className={offer.class}></i> {offer.name}</li>
                              );
                      })} */}
                </ul>
                {auth == false &&
                 <div className="card-course-btns">
                  {/* <Link to={window.PUBLIC_URL+'/buy_course/'+btoa(course.id)} className="btn btn-sm" id={course.id}>Buy Now</Link>
                  <Link to={window.PUBLIC_URL+'/gift_course/'+btoa(course.id)} className="btn btn-sm">Gift To Friend </Link> */}
                  <Link to={'/course_details/'+btoa(course.id)} className="btn btn-sm">Read more </Link>
                  <Link to={'/contact-us'} className="btn btn-sm">Contact us</Link>
                </div>
                }
                {auth !== false &&
                 <div className="card-course-btns">
                  {isPurchased == true ? 
                  <Link to={'/user/course-details/'+btoa(course.id)} className="btn btn-sm" id={course.id}>Start Course</Link>
                  : 
                  <Link to={'/user/buy_course/'+btoa(course.id)} className="btn btn-sm" id={course.id}>Buy Now</Link>
                 }
                  
                  <Link to={'/user/gift_course/'+btoa(course.id)} className="btn btn-sm">Gift To Friend </Link>
                  <Link to={'/user/contact-us'} className="btn btn-sm">Contact Us</Link>
                </div>
                }
              </div>
            </div>
          </div>
		);
}

export default CourseListDetails;