import React from 'react';

function HandoutPdfDetails(props) {
    let divClass = '';
    if (props.checkPreLaunch === true && props.pdf.pre_launch === '0') {
        divClass = 'col-xl-4 col-lg-4 col-md-3 col-sm-6 disableData';
    } else {
        divClass = 'col-xl-4 col-lg-4 col-md-3 col-sm-6';
    }
    return (
        <div className={divClass}>
            <div className="hoverable card">
            <div className="imgvid-div">
                <img src={props.pdf.thumbnail} className="img-fluid image scale-on-hover moduleContentImage" />
                </div>
                <div className="card-content">
                    <span className="card-title d-block mb-2">{props.pdf.title}</span>
                    <p>{props.pdf.description}</p>
                </div>
                <div className="text-right col-xl-12 mb-3">
                    <a className="btn btn-sm waves-effect waves-light" href={props.pdf.pdf} target="_blank">View</a>
                </div>
            </div>
        </div>
    );
}

export default HandoutPdfDetails;