import React,{Component} from 'react';
import { connect } from 'react-redux';
import {updateUserDetails} from '../../actions/userAction';
import { getLocalStorageAuth } from '../../utils/helpers';
import SimpleReactValidator from 'simple-react-validator';
import UserServices from '../../services/userServices';

class UserAccountDetailForm extends Component{

	constructor(props){
		super(props);
	    this.validator = new SimpleReactValidator();
	    const userData = getLocalStorageAuth();
	    if(userData){
		    this.state = {
		    	id: userData.userDetails.id,
		  		username : userData.userDetails.username,
		        firstName: userData.userDetails.first_name,
		        lastName: userData.userDetails.last_name,
		        email : userData.userDetails.email,
		        address : userData.userDetails.address,
		        city: userData.userDetails.city,
		        country: userData.userDetails.country,
		        countryCode: userData.userDetails.country_code,
		        phone: userData.userDetails.phone,
		        zip: userData.userDetails.zip_code,
		        usernameAvailability:true,
	        	emailAvailability:true,
	        	oldUsername:userData.userDetails.username,
	        	oldEmail:userData.userDetails.email,
	 	 }	
	    }else{
	    	this.props.history.push('/userlogin')
	    }
	   
	  this.onChange = this.onChange.bind(this);
	  this.onSubmit = this.onSubmit.bind(this);
	  this.onEmailChange = this.onEmailChange.bind(this);
	  this.onUsernameChange = this.onUsernameChange.bind(this);
	}

	componentDidMount() {
    window.scrollTo(0, 0);
	}

	onChange(e){
		this.setState({ [e.target.name] : e.target.value });
	}

	onSubmit(e){
		e.preventDefault();
		if (!this.validator.allValid()) {
            this.validator.showMessages();
            this.forceUpdate();
            return false;
        }	
     if(!this.state.usernameAvailability){
        return false;
     }  
     if(!this.state.emailAvailability){
        return false;
     }   
    	const userDetails = {
    		id : this.state.id,
		    	username : this.state.username,
	        first_name: this.state.firstName,
	        last_name: this.state.lastName,
	        email : this.state.email,
	        address : this.state.address,
	        city: this.state.city,
	        country: this.state.country,
	        country_code: this.state.countryCode,
	        phone: this.state.phone,
	        zip_code: this.state.zip,
		}	
        this.props.updateUserDetails(userDetails);
        window.scrollTo(0, 0);
	}

	onUsernameChange(e){
     const userName = e.target.value;
     this.setState({ username : userName });
     UserServices.checkUsernameAvailability(userName, this.state.oldUsername)
     .then(res =>{
        if(res.data.status !== true){
          this.setState({ usernameAvailability : false });
        }else{
           this.setState({ usernameAvailability : true });
        }
    })
     
  	}

  	onEmailChange(e){
     const email = e.target.value;
     this.setState({ email : email });
     UserServices.checkEmailAvailability(email, this.state.oldEmail)
     .then(res =>{
        if(res.data.status !== true){
          this.setState({ emailAvailability : false });
        }else{
           this.setState({ emailAvailability : true });
        }
    })
     
  }

	render(){
		const {alert} = this.props;
		return(
			<>  
      {this.state.emailAvailability == false &&
      <div className="col-sm-6">
      <div className="alert alert-danger alert-dismissible fade show" role="alert">
             <p>Email is already in used.</p>
            </div></div>}
          {this.state.usernameAvailability == false &&
             <div className="col-sm-6"><div className="alert alert-danger alert-dismissible fade show" role="alert">
             <p>Username is already in used.</p>
            </div></div>} 
       <form id="user-details-form" className="form-validate form-horizontal" onSubmit={this.onSubmit}>
                <div className="row detail-form">
                  <div className="input-field col-md-6">
                    <label id="username-lbl" for="firstname" className="active required">First Name<span className="star">&nbsp;*</span></label>
                    <input type="text" name="firstName" id="firstname" value={this.state.firstName} onChange={this.onChange}/>
                     {this.validator.message('firstName', this.state.firstName, 'required|alpha|max:50')}
                  </div>
                  <div className="input-field col-md-6">
                    <label id="lastName-lbl" for="lastName" className="active required">Last Name<span className="star">&#160;*</span></label>
                    <input type="text" name="lastName" id="lastName" value={this.state.lastName} onChange={this.onChange}/>
                    {this.validator.message('lastName', this.state.lastName, 'required|alpha|max:50')}
                  </div>
                  <div className="input-field col-md-6">
                    <label id="email-lbl" for="email" className="active required">Email ID<span className="star">&#160;*</span></label>
                    <input type="text" name="email" id="email" value={this.state.email} onChange={this.onEmailChange}/>
                  {this.validator.message('email', this.state.email, 'required|email')}
                  </div>
                  <div className="col-md-6">
                    <div className="row">
                      <div className="input-field col-md-6">
                       <label id="country-lbl" for="country" className="active required">Country</label>
                       <input type="text" name="country" id="country" value={this.state.country} onChange={this.onChange}/>
                        
                      </div>
                        <div className="input-field col-md-6">
                        <label id="countryCode-lbl" for="countryCode" className="active required">Country Code</label>
                       	 <input type="number" name="countryCode" id="countryCode" value={this.state.countryCode} onChange={this.onChange}/>
                        
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6 input-field ">
                     <label id="phone-lbl" for="phone" className="active required">Phone Number</label>
                     <input type="number" name="phone" id="phone" value={this.state.phone} onChange={this.onChange}/>
           
                  </div>
                   <div className="input-field col-md-6">
                        <label id="zip-lbl" for="zip" className="active required">Zip Code</label>
                        <input type="number" name="zip" id="zip" value={this.state.zip} onChange={this.onChange}/>
                       
                  </div>
                  <div className="col-md-6 input-field ">
                     <label id="address-lbl" for="address" className="active required">Address</label>
                     <input type="text" name="address" id="address"  value={this.state.address} onChange={this.onChange}/>
                    
                  </div>
                  <div className="input-field col-md-6">
                  <label id="city-lbl" for="city" className="active required">City</label>
                    <input type="text" name="city" id="city"  value={this.state.city} onChange={this.onChange}/>
                    
                  </div>
                  <div className="input-field col-md-6">
                     <label id="username-lbl" for="username" className="active required">User Name<span className="star">&#160;*</span></label>
                     <input type="text" name="username" id="username"  value={this.state.username} onChange={this.onUsernameChange}/>
                      {this.validator.message('username', this.state.username, 'required')}
                   </div>
                    <div className="col-md-12 text-right">
                        <button className="btn btn-lg" type="submit">Update</button>
                    </div>
                </div>
                </form>
                     
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {updateUserDetails})(UserAccountDetailForm);