import React,{Component} from 'react';
import axios from 'axios';
import { apiRoute, getApiHeader } from '../../utils/helpers';
import Header from '../../components/frontend/common/Header';
import Footer from '../../components/frontend/common/Footer';
import Banner from '../../components/frontend/common/Banner';
import CourseDetailInfo from '../../components/frontend/course/CourseDetailInfo';
import BuyCourseForm from '../../components/frontend/course/BuyCourseForm';

class BuyCourse extends Component{

	constructor(props){
    	super(props);
	      this.state = {
	      	banner : {},
		    } 
		  }


	componentWillMount() {

   	 window.scrollTo(0, 0);

   	 const requestOptions = {
			headers : getApiHeader()
		};
		axios.get(apiRoute('cms-page-banner/'+btoa(13)),requestOptions)
		.then(res =>{
			this.setState({ banner : res.data });
	    })
	}
	render(){
		const courseId = this.props.match.params.courseId;
		const affiliateUrlId = this.props.match.params.affiliateId;
		return(
			<>
			<Header/>
			<Banner banner={this.state.banner}/>
			<main>
			<section className="sec-cinfo">
      			<div className="container">
      				<CourseDetailInfo courseId={courseId} affiliateUrlId={affiliateUrlId}  history={this.props.history}/>
      				<BuyCourseForm affiliateUrlId={affiliateUrlId} courseId={courseId}/>
      			</div>
      		</section>
			</main>
			<Footer/>
			</>
			);
	}
}

export default BuyCourse;