import React,{Component} from 'react';
import { connect } from 'react-redux';
import AOS from 'aos';
import {Helmet} from "react-helmet";
import Footer from '../../components/frontend/common/Footer';
import RoadToDharmaHeader from '../../components/frontend/common/RoadToDharmaHeader';
import ContactUsForm from '../../components/frontend/contactus/ContactUsForm';

class Contactus extends Component{


	componentDidMount() {
	window.scrollTo(0, 0);
	AOS.init();
	}

	render(){
		return(
			<>
			<Helmet>
                <meta charSet="utf-8" />
                <title>Learn more about our Online Courses.</title>
                <link rel="canonical" href="https://courses.sattvaconnect.com/contact-us" />
				<meta name="keywords" content="meditation lessons online, online yoga training," />
				<meta name="description" content="Immerse yourself in a vide variety of Online Courses to deepen your knowlegde about the Authentic Teachings and Full-Spectrum Practices of Yoga. " />
            </Helmet>
			<RoadToDharmaHeader/>
			<ContactUsForm/>
			<Footer/>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {})(Contactus);