import React, { Component } from 'react';
import { connect } from 'react-redux';
import { clearAllAlerts, deleteUserAccount } from '../../actions/userAction';
import { getLocalStorageAuth } from '../../utils/helpers';
import AOS from 'aos';
import {Helmet} from "react-helmet";
import Header from '../../components/user/Header';
import DharmaFooter from '../../components/frontend/common/DharmaFooter';
import UserAccountDetailForm from '../../components/user/UserAccountDetailForm';
import UserChangePasswordForm from '../../components/user/UserChangePasswordForm';

class AccountDetails extends Component {

	constructor(props) {
		super(props);
		this.props.clearAllAlerts();
		const userData = getLocalStorageAuth();
		if (userData) {
			this.state = {
				userId: userData.userDetails.id,
				userName: userData.userDetails.first_name,
			}
		} else {
			this.props.history.push('/userlogin')
		}
		this.onDeleteClick = this.onDeleteClick.bind(this);
	}


	componentDidMount() {
		window.scrollTo(0, 0);
		AOS.init();
	}

	onDeleteClick(e) {
		this.props.deleteUserAccount(this.state.userId, this.props.history);
	}

	render() {
		const { alert } = this.props;
		return (
			<>
			<Helmet>
                <meta charSet="utf-8" />
                <title>Account Details.</title>
                <link rel="canonical" href="https://courses.sattvaconnect.com/user/account-details" />
				<meta name="description" content="" />
            </Helmet>
				<Header  history={this.props.history}/>
				<main className="account-details">
					<section className="sec pb-0">
						<div className="container mt-5">
							<h4>Welcome to Sattva Connect</h4>
							<div className="card card-panel">
								<div className="col-md-6">
									{alert.message && alert.type == 'error' &&
										<div className="alert alert-danger alert-dismissible fade show " role="alert">
											{alert.message}

										</div>}
									{alert.message && alert.type == 'success' &&
										<div className="alert alert-success alert-dismissible fade show" role="alert">
											{alert.message}

										</div>}
								</div>
								<div className="text-right">
									<button className="btn btn-sm my-3" type="btn" name="action" data-toggle="modal" data-target="#delete-account">Delete my account</button>
								</div>
								<ul className="nav nav-tabs custom-tabs" id="myTab" role="tablist">
									<li className="nav-item">
										<a className="nav-link active" id="account_details-tab" data-toggle="tab" href="#account_details" role="tab" aria-controls="account_details"
											aria-selected="true">Account Details</a>
									</li>
									<li className="nav-item">
										<a className="nav-link" id="change_password-tab" data-toggle="tab" href="#change_password" role="tab" aria-controls="change_password"
											aria-selected="false">Change Password</a>
									</li>
								</ul>
								<div className="tab-content" id="myTabContent">
									<div className="tab-pane fade show active pt-5" id="account_details" role="tabpanel" aria-labelledby="account_details-tab">
										<UserAccountDetailForm />
									</div>
									<div className="tab-pane fade" id="change_password" role="tabpanel" aria-labelledby="change_password-tab">
										<UserChangePasswordForm userId={btoa(this.state.userId)} />
									</div>
								</div>
							</div>
						</div>
					</section>
					<DharmaFooter/>
				</main>
				<div className="modal fade" id="delete-account" tabindex="-1" role="dialog" aria-labelledby="manage-methodTitle" aria-hidden="true">
					<div className="modal-dialog modal-lg modal-dialog-centered" role="document">
						<div className="modal-content">
							<div className="modal-body">
								<h6 className="mb-3">Delete Sattva Account Permanently</h6>
								<p>Hi {this.state.userName},</p>
								<p>We are sorry that you have decided to cancel your subscription. Please keep in mind
									that if you choose to reactivate your account at a later date, the rates at that time
									will apply to your membership.
									</p>
								<div className="text-right">
									<a className="pop btn btn-sm red" data-container="body" data-toggle="popover" data-placement="top" data-content="Delete anyway" data-dismiss="modal" onClick={this.onDeleteClick}><i className="fas fa-trash"></i></a>
									<button type="button" className="btn btn-sm" data-dismiss="modal"><i className="fas fa-times"></i></button>
								</div>
							</div>
						</div>
					</div>
				</div>

			</>
		);
	}
}

const mapStateToProps = (state) => ({
	alert: state.alert,
});
export default connect(mapStateToProps, { clearAllAlerts, deleteUserAccount })(AccountDetails);