import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import initialState from './reducers/initialState';
import configureStore, { history } from './store';
import PageNotFound from './pages/frontend/PageNotFound';
import UserLogout from './pages/frontend/UserLogout';
import Courses from './pages/frontend/Courses';
import BuyCourse from './pages/frontend/BuyCourse';
import GiftCourse from './pages/frontend/GiftCourse';
import CoursePayment from './pages/frontend/CoursePayment';
import PaymentSuccess from './pages/frontend/PaymentSuccess';
import RegistrationSuccess from './pages/frontend/RegistrationSuccess';
import FrontendContactus from './pages/frontend/Contactus';
import TermsOfServices from './pages/frontend/TermsOfServices';
import UserLogin from './pages/user/UserLogin';
import ForgotPassword from './pages/user/ForgotPassword';
import MyCourses from './pages/user/MyCourses';
import CourseInfoDetails from './pages/user/CourseInfoDetails';
import AvailableCourses from './pages/user/AvailableCourses';
import CourseDetails from './pages/user/CourseDetails';
import RoadToDharmaFullCourse from './pages/user/RoadToDharmaFullCourse';
import UserCoursePayment from './pages/user/CoursePayment';
import Contactus from './pages/user/Contactus';
import AccountDetails from './pages/user/AccountDetails';
import UserGiftCourse from './pages/user/GiftCourse';
import ResetPassword from './pages/user/ResetPassword';

import HomePage from './pages/frontend/Home';

function App() {
  const store = configureStore(initialState);

  return (
    <>
      <Provider store={store}>
        {/* <ConnectedRouter  history={history}> */}
        <Switch>
          {/* <Route exact path="/" component={Index}/> */}
          <Route exact path='/' component={Courses} />
          <Route
            exact
            path='/course_details/MQ==/:affiliateId?'
            component={HomePage}
          />
          <Route
            exact
            path='/buy_course/:courseId/:affiliateId?'
            component={BuyCourse}
          />
          <Route exact path='/gift_course/:courseId' component={GiftCourse} />
          <Route
            exact
            path='/payment/:courseId/:userId'
            component={CoursePayment}
          />
          <Route exact path='/payment_success' component={PaymentSuccess} />
          <Route
            exact
            path='/registration_success'
            component={RegistrationSuccess}
          />
          <Route exact path='/userlogin' component={UserLogin} />
          <Route exact path='/forgot-password' component={ForgotPassword} />
          <Route exact path='/contact-us' component={FrontendContactus} />
          <Route exact path='/user/dashboard' component={MyCourses} />
          <Route
            exact
            path='/user/available-courses'
            component={AvailableCourses}
          />
          <Route
            exact
            path='/user/course-details/:courseId'
            component={RoadToDharmaFullCourse}
          />
          <Route
            exact
            path='/user/course-info/:courseId'
            component={CourseInfoDetails}
          />
          <Route
            exact
            path='/user/course-info-details/:courseId'
            component={CourseDetails}
          />
          <Route exact path='/user/contact-us' component={Contactus} />
          <Route
            exact
            path='/user/account-details'
            component={AccountDetails}
          />
          <Route
            path='/user/buy_course/:courseId'
            component={UserCoursePayment}
          />
          <Route
            exact
            path='/user/gift_course/:courseId'
            component={UserGiftCourse}
          />
          <Route
            exact
            path='/reset-password/:userId'
            component={ResetPassword}
          />
          <Route exact path='/user/logout-user' component={UserLogout} />
          <Route exact path='/terms-of-services' component={TermsOfServices} />

          <Route exact path='/home-page' component={HomePage} />

          <Route component={PageNotFound} />
        </Switch>
        {/* </ConnectedRouter> */}
      </Provider>
    </>
  );
}

export default App;
