import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import AOS from 'aos';
import {Helmet} from "react-helmet";
import {Redirect} from 'react-router-dom';
import { fetcCourseAllDetails } from '../../actions/courseAction';
import { ToastsContainer, ToastsStore, ToastsContainerPosition } from 'react-toasts';
import { apiRoute, getApiHeader } from '../../utils/helpers';
import Header from '../../components/user/Header';
import RoadToDharmaFullCourseComponent from '../../components/user/RoadToDharmaFullCourseComponent';
import { getLocalStorageAuth } from '../../utils/helpers';

class RoadToDharmaFullCourse extends Component {

	constructor(props) {
		super(props);
		this.state = {
			banner: {},
			userId: '',
			redirect: false,
		}
	}


	componentDidMount() {
		AOS.init();
		window.scrollTo(0, 0);
		const requestOptions = {
			headers: getApiHeader()
		};
		axios.get(apiRoute('cms-page-banner/' + btoa(17)), requestOptions)
			.then(res => {
				this.setState({ banner: res.data });
			})

		// let userId = getUserId(this.props.history);
		//this.props.fetcCourseAllDetails(this.props.match.params.courseId);
		//this.props.fetchRelatedCourses(userId,this.props.match.params.courseId);
		let authData = getLocalStorageAuth();
		if (authData) {
			this.setState({ userId: authData.userDetails.id });
		} else {
			this.setState({ redirect: true });
		}
	}
	render() {
		if (this.state.redirect) {

			return (
				<Redirect to= '/' />
			);

		}
		const courseId = this.props.match.params.courseId;
		const { alert } = this.props;
		return (
			<>
			<Helmet>
                    <meta charSet="utf-8" />
                    <title>The Road To Dharma - DocuSeries and Online Course </title>
                    <link rel="canonical" href="https://courses.sattvaconnect.com/course_details/MQ==" />
                    <meta name="keywords" content="The Road to Dharma, Documentary Series in India, Finding Freedom Online Course, What is Dharma," />
                    <meta name="description" content="Documentary series In India, that projects enlightenment and entertaining series of online courses with innovative formats, sacred sites, and teachings." />
                </Helmet>
				{alert.message && alert.type == 'success' &&
					ToastsStore.success(alert.message)}
				{alert && alert.type == 'error' &&
					ToastsStore.error('Something went wrong')}
				<ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT} />
				<Header  history={this.props.history}/>
				<main className="admin-content">
					<RoadToDharmaFullCourseComponent userId={this.state.userId} courseId={courseId} />
				</main>
				<div id="myvidModal" className="modal fade myvidModal" role="dialog">
					<div className="modal-dialog modal-lg">
						<div className="modal-content">
							<div className="modal-header">
								<button type="button" className="closeVideo" data-dismiss="modal">
									<span aria-hidden="true">&times;</span>
								</button>
							</div>
							<div className="modal-body">
								<iframe id="videoIframe" src="" width="100%" height="550" frameborder="0" allow="autoplay; fullscreen" webkitallowfullscreen="true" mozallowfullscreen="true" allowfullscreen="true"></iframe>
							</div>
						</div>
					</div>
				</div>

				<div id="myAudioModal" class="modal fade myAudioModal" role="dialog">
					<div class="modal-dialog modal-lg">
						<div class="modal-content">
							<div class="modal-header">
								<button type="button" class="audioPlayer closeAudio" data-dismiss="modal">
									<span aria-hidden="true">&times;</span>
								</button>
							</div>
							<div class="modal-body">
								<div class="audioimg-div">
									<img id="audioModalImage" class="img-fluid" src="../../images/classes/760204525_480.jpg" />
								</div>
								<div class="audio-div" id="audioDiv">
								</div>
							</div>
						</div>
					</div>
				</div>

			</>
		);
	}
}

const mapStateToProps = (state) => ({
	courseDetails: state.courses.allDetails,
	alert: state.alert,
});
export default connect(mapStateToProps, { fetcCourseAllDetails })(RoadToDharmaFullCourse);