import React,{Component} from 'react';
import { connect } from 'react-redux';
import { InfiniteScroll } from 'react-simple-infinite-scroll';
import {ToastsContainer, ToastsStore, ToastsContainerPosition} from 'react-toasts';
import {fetchUserAvailableCourses} from '../../actions/courseAction';
import { getUserId } from '../../utils/helpers';
import CourseListDetails from '../../components/user/CourseListDetails';

class AvailableCoursesList extends Component{

	componentDidMount(){
		let userId = getUserId(this.props.history);
		this.props.fetchUserAvailableCourses(userId,0,[]);
	  }
	  loadMore = () => {
		let userId = getUserId(this.props.history);  
		this.props.fetchUserAvailableCourses(userId,this.props.cursor, this.props.courses);
	  } 

	render(){
		const { alert } = this.props;
		return(
			<>
			{alert.message && alert.type == 'success' &&
                  ToastsStore.success(alert.message) }
            {alert && alert.type == 'error' &&
                  ToastsStore.error('Something went wrong') } 
            <ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT}/>      
			<section className="sec most-courses">
		    	<div className="container">
		        	<div className="row">
					<InfiniteScroll
						throttle={100}
						threshold={300}
						isLoading={alert.isloading}
						hasMore={this.props.hasMore}
						onLoadMore={this.loadMore}
						>
						{this.props.courses.map((item,index)=>{
						return(
							<CourseListDetails key={index} course={item	} purchased={false}/>
							);
						})}	
						</InfiniteScroll>
					 {this.props.courses && this.props.courses.length == 0 ? <h4>No Data Found</h4> : ''}
					</div>
				</div>
			</section>	
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  courses: state.userDetails.availableCourses,
  alert: state.alert,
  cursor:state.userDetails.cursor,
  hasMore:state.userDetails.hasMore,
});

export default connect(mapStateToProps, { fetchUserAvailableCourses })(AvailableCoursesList);