import React,{Component} from 'react';
import { connect } from 'react-redux';
import OwlCarousel from 'react-owl-carousel';
import CourseModuleDetails from '../../components/user/CourseModuleDetails';

class CourseAllDetails extends Component{
	render(){
		return(
			<>     
			<section className="sec most-courses">
		    	<div className="accordion custom-accordion course-accordion" id="accordionExample">
	        		<div className="card">
		        	{this.props.course.modules && this.props.course.modules.map((item,index)=>{
					return(
		        		<CourseModuleDetails key={index} number={index} isParchased={this.props.isParchased} module={item}/>
						);
					})}	
					</div>
				</div>
			</section>
			{this.props.relatedCourses && this.props.relatedCourses.length !== 0 && this.props.isParchased !== 0? 
				<section className="sec related-courses">
			        <div className="container">
			            <h4>Related Courses</h4>
			            <OwlCarousel className="owl-theme course-slider" loop margin={10} nav={true} items={4}>
			            {this.props.relatedCourses.map((items,index) => {
			                 return(
				                 <div className="item" key={index}>
					                <a href={'/user/course-info/'+btoa(items.id)}  className="hoverable card">
					                    <img className="img-fluid" src={items.image}/>
					                    <div className="card-content">
					                      <span className="card-title">{items.title}</span>
					                      <p>{items.description}</p>
					                   </div>
					                </a>
					            	</div>
			                     );
			                })}
		            	</OwlCarousel>
		            </div>
	            </section>
	             : ''}
			</>
			);
		}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {})(CourseAllDetails);