import React from 'react';
import CoursesTabs from '../../components/user/CoursesTabs';
import VideoDetails from '../../components/user/course/VideoDetails';
import TranscriptDetails from '../../components/user/course/TranscriptDetails';
import HandoutPdfDetails from '../../components/user/course/HandoutPdfDetails';
import AudioDetails from '../../components/user/course/AudioDetails';

function CourseModuleDetails({ number, module, isParchased }) {
	let videos = module.videos;
	let audios = module.audios;
	let transcripts = module.transcripts;
	let handoutPdfs = module.handoutPdfs;
	let checkPreLaunch = false;
	if(isParchased === 0){
		if(module.pre_launch === '0'){
			checkPreLaunch = true;
		}
	}
	console.log(checkPreLaunch);
	return (
		<>
			<div className="z-depth-0">
				<div className="card-header p-0" id="headingOne">
					<h5 className="mb-0">
						<a className="btn-link" data-toggle="collapse" data-target={'#collapseOne' + number} aria-expanded="false" aria-controls={'collapseOne' + number}>{module.title}</a>
					</h5>
				</div>
				<div id={'collapseOne' + number} className="collapse" aria-labelledby="headingOne" data-parent="#accordionExample">
					<div className="card-body">
						<div className="">
							<CoursesTabs number={number} />
							<div className="tab-content" id="myTabContent">
								<div className="tab-pane fade show active" id={'module-info' + number} role="tabpanel" aria-labelledby={'module-info-tab' + number}>
									<div className="videos-intabs">
										<div className="row">
											<div className="col-md-4 col-lg-4">
												<div className="videos_img">
													<img src={module.image} className="img-fluid" />
												</div>
											</div>
											<div className="col-md-8 col-lg-8">
												<h5 className="mb-3">{module.title}</h5>
												<p>{module.description}</p>
											</div>
										</div>
									</div>
								</div>
								<div className="tab-pane fade" id={'videos' + number} role="tabpanel" aria-labelledby="videos-tab1">
									{videos && videos.map((item, index) => {
										return (
											<VideoDetails video={item} key={item.id} checkPreLaunch={checkPreLaunch}/>
										);
									})}
									{videos && videos.length === 0 ? <div className="no_found mt-3">No Data Found</div> : ''}


								</div>
								<div className="tab-pane fade" id={'audio' + number} role="tabpanel" aria-labelledby="audio-tab1">
									{audios && audios.map((item, index) => {
										return (
											<AudioDetails audio={item} key={item.id} checkPreLaunch={checkPreLaunch}/>
										);
									})}
									{audios && audios.length === 0 ? <div className="no_found mt-3">No Data Found</div> : ''}
								</div>
								<div className="tab-pane fade" id={'transcripts' + number} role="tabpanel" aria-labelledby="transcripts-tab1">
									<div className="row">
										{transcripts && transcripts.map((item, index) => {
											return (
												<TranscriptDetails transcript={item} key={item.id} checkPreLaunch={checkPreLaunch}/>
											);
										})}
										{transcripts && transcripts.length === 0 ? <div className="col-md-12"><div className="no_found mt-3">No Data Found</div></div> : ''}
									</div>
								</div>
								<div className="tab-pane fade" id={'handout-pdf' + number} role="tabpanel" aria-labelledby="handout-pdf-tab1">
									<div className="row">
										{handoutPdfs && handoutPdfs.map((item, index) => {
											return (
												<HandoutPdfDetails pdf={item} key={item.id} checkPreLaunch={checkPreLaunch}/>
											);
										})}
										{handoutPdfs && handoutPdfs.length === 0 ? <div className="col-md-12"><div className="no_found mt-3">No Data Found</div></div> : ''}
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

export default CourseModuleDetails;