import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { Helmet } from "react-helmet";
import cookie from 'react-cookies';
import { ToastsContainer, ToastsStore, ToastsContainerPosition } from 'react-toasts';
import SimpleReactValidator from 'simple-react-validator';
import * as authAction from '../../actions/authAction';
import { clearAllAlerts } from '../../actions/userAction';
import RoadToDharmaHeader from '../../components/frontend/common/RoadToDharmaHeader';

class UserLogin extends Component {

	constructor(props) {
		super(props);
		this.props.clearAllAlerts();
		this.validator = new SimpleReactValidator();
		this.onChange = this.onChange.bind(this);
		this.onSubmit = this.onSubmit.bind(this);
		this.state = {
			username: '',
			password: '',
			labelClass:'',
		}
	}
	componentDidMount() {
		const cookieUsername = cookie.load('username');
		const cookiePassword = cookie.load('password');
		if(cookieUsername !== undefined){
			this.setState({ username: cookieUsername });
			this.setState({ labelClass: 'active' });
		}
		if(cookiePassword !== undefined){
			this.setState({ password: cookiePassword });
			this.setState({ labelClass: 'active' });
		}
		if(this.props.location.state !== undefined){
			ToastsStore.success('It seems that you have already registered, Please click login to access your Dashboard.',4000);
		}
		window.scrollTo(0, 0);
	}

	onChange(e) {
		this.setState({ [e.target.name]: e.target.value });
	}

	onSubmit(e) {
		e.preventDefault();
		if (!this.validator.allValid()) {
			this.validator.showMessages();
			this.forceUpdate();
			return false;
		}
		this.props.login(this.state.username, this.state.password, this.props.history)

	}

	render() {
		const { alert, courseDetail } = this.props;
		return (
			<>
			<ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT} />
				<Helmet>
					<meta charSet="utf-8" />
					<title>Login to access your Sattva Connect Online Courses</title>
					<link rel="canonical" href="https://courses.sattvaconnect.com/userlogin" />
					<meta name="keywords" content="Audio Meditation Practices, yogic teachings and practices" />
					<meta name="description" content="Unlimited Access - Access your Online Courses anywhere, anytime. " />
				</Helmet>
				{alert && alert.isloading &&
					<div className="preloader-background">
						<div class="big sattva_loader active">
							<img src={require('../../assets/images/loader.png')} />
						</div>
					</div>
				}
				<RoadToDharmaHeader />
				<main className="admin-content">
					<section className="sec">
						<div className="container">
							<div className="row justify-content-center">
								<div className="col-md-7 col-sm-12">
									{alert.message && alert.type === 'error' &&
										<div className="alert alert-danger" role="alert">
											{alert.message}
										</div>}
									{alert.message && alert.type === 'success' &&
										<div className="alert alert-success" role="alert">
											{alert.message}
										</div>}
									<div className="card subscription-card customer-support">
										<form autocomplete="off" className="form form-horizontal" onSubmit={this.onSubmit}>
											<h4>Member Login</h4>
											<div className="">
												<div className="input-field">
													<label id="username-lbl" for="username" className={'required '+ this.state.labelClass} >Username or Email<span className="star">&#160;*</span></label>
													<input type="text" name="username" id="username" value={this.state.username} onChange={this.onChange} />
													{this.validator.message('username', this.state.username, 'required')}
												</div>
											</div>
											<div className="">
												<div className="input-field">
													<label id="password-lbl" for="password" className={'required '+ this.state.labelClass}>Password<span className="star">&#160;*</span></label>
													<input type="password" name="password" id="password" aria-required="true" value={this.state.password} onChange={this.onChange} />
													{this.validator.message('password', this.state.password, 'required')}
												</div>
											</div>
											<div className="login-links">
												<div className="input-field">
													<Link to="forgot-password">Don't remember your password? Let's reset, Click here to reset now</Link>
												</div>
											</div>
											<div className="">
												<div className="input-field s12">
													<button type="submit" className="btn btn-lg">LOGIN</button>
												</div>
											</div>
											<div>
												<br/>
												<p>If you have already registered for the free episodes,You can Login with the Temp User ID, Password that we sent you (If you haven't changed it from Accounts Details Page).<br/>Search your Inbox and Spam for Sattva Connect to find that email.<br/> Or Click the above link to Reset your details now.</p>
											</div>
										</form>
									</div>
								</div>
							</div>
						</div>
					</section>
				</main>
			</>
		);
	}
}

const mapStateToProps = (state) => ({
	alert: state.alert,
});
const mapActionsToProps = ({
	login: authAction.login,
	clearAllAlerts: clearAllAlerts,
})
export default connect(mapStateToProps, mapActionsToProps)(UserLogin);