import React, { Component } from 'react';
import { connect } from 'react-redux';
import AOS from 'aos';
import { Helmet } from "react-helmet";
import DropIn from "braintree-web-drop-in-react";
import SimpleReactValidator from 'simple-react-validator';
import Header from '../../components/user/Header';
import { getLocalStorageAuth } from '../../utils/helpers';
import UserServices from '../../services/userServices';
import DharmaFooter from '../../components/frontend/common/DharmaFooter';
import { buyCourseWithRegistration, getClintId, fetchCourseDetail } from '../../actions/courseAction';
import { ToastsContainer, ToastsStore, ToastsContainerPosition } from 'react-toasts';

class CoursePayment extends Component {

  constructor(props) {
    super(props);
    this.onCheckboxChange = this.onCheckboxChange.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onUsernameChange = this.onUsernameChange.bind(this);
    this.validator = new SimpleReactValidator();
    this.state = {
      terms: '',
      username: '',
      password: '',
      usernameAvailability: true,
      userId: this.props.match.params.userId,
    }
  }

  onCheckboxChange(e) {
    this.setState({ terms: e.target.checked });
  }

  async componentDidMount() {
    window.scrollTo(0, 0);
    AOS.init();
    this.props.getClintId(this.props.history);
    this.props.fetchCourseDetail(this.props.match.params.courseId, this.props.history);
    const script = document.createElement("script");
    script.src = "https://seal.godaddy.com/getSeal?sealID=4KKbLJiyghS3CThDtwZAU1Ag5eWJxO8tr4sjnneQIWiPW0UJ0aSb5u4AozJM";
    script.async = true;
    let scriptDiv = document.getElementById("siteseal");
    scriptDiv.appendChild(script);
  }

  onUsernameChange(e) {
    const userName = e.target.value;
    console.log(userName);
    this.setState({ username: userName });
    const userData = getLocalStorageAuth();
    UserServices.checkUsernameAvailability(userName, userData.userDetails.username)
      .then(res => {
        if (res.data.status !== true) {
          this.setState({ usernameAvailability: false });
        } else {
          this.setState({ usernameAvailability: true });
        }
      })

  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  registerUserWithPayPal(paymentDetails) {
    const data = {
      userId: this.state.userId,
      username: this.state.username,
      userPassword: this.state.password,
      paymentDetail: paymentDetails,
      course: this.props.course,
      paymentType: 'PayPal',
    }
    this.props.buyCourseWithRegistration(data, this.props.history)
  }

  registerUserWithCreditCard(paymentDetails) {

    const data = {
      userId: this.state.userId,
      username: this.state.username,
      userPassword: this.state.password,
      paymentDetail: paymentDetails,
      course: this.props.course,
      paymentType: 'Credit Card',
    }
    this.props.buyCourseWithRegistration(data, this.props.history)
  }

  async buy() {
    if (!this.validator.allValid()) {
      this.validator.showMessages();
      this.forceUpdate();
      return false;
    }
    if (!this.state.usernameAvailability) {
      return false;
    }
    const { nonce } = await this.instance.requestPaymentMethod();
    //await fetch(`https://betasite.online/sattva-courses/api/make-card-payment/${nonce}`);
    this.registerUserWithCreditCard(nonce)
  }
  render() {
    const userData = getLocalStorageAuth();
    const { alert, course } = this.props;

    const onSuccess = (payment) => {
      console.log("The payment was succeeded!");
      this.registerUserWithPayPal(payment)
    }

    const onCancel = (data) => {
      ToastsStore.error('Something went wrong. Please try again')
    }

    const onError = (err) => {
      ToastsStore.error('Something went wrong. Please try again')
    }

    let env = 'sandbox'; // you can set here to 'production' for production
    let currency = 'USD'; // or you can set this value from your props or state
    let total = parseInt(167); // same as above, this is the total amount (based on currency) to be paid by using Paypal express checkout

    const client = {
      sandbox: 'AWavhxe0MxWahWF7k0huWecDCiHFTfo8_R4194IlA1o3MmaP81GIVOhLDns99n29UM4PwTXjlccPYzOH',
      production: 'AQ4aGjXXxDa-xypY1wgpbw1nL0Ph4VyKDTIIPq_xzJ9v7OndwSv6FPiZ1APE-KzdpLoasTFihr2SsRtc',
    }
    const clintId = this.props.braintree;

    return (
      <>
        <Helmet>
          <meta charSet="utf-8" />
          <title>The Road To Dharma | Get access to your courses today!</title>
          <meta name="description" content="Course payment." />
        </Helmet>
        {alert && alert.type === 'error' &&
          ToastsStore.error('Something went wrong. Please try again')}
        <ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT} />
        <Header />
        <main class="contact-us">
          <section className="sec mb-20">
            <div className="sec pb-0 container">
              <div className="">
                <div className="col-sm-12">
                  <h4 className="paymentHead">Payment Summary</h4>
                  <div className="contact-form">
                    <div className="card">
                      <div className="customer-support">
                        <legend></legend>
                        <div className="row">
                          <div className="col-sm-6">
                            <b>Course Name: </b>{course.title}<hr />
                            <b>Course Price: </b>${course.price}<hr />
                            {/* <b>Special Launch Price: </b>${course.price_to_pay}<hr /> */}
                            <b>Teacher Name: </b>ANAND MEHROTRA, ADAM SCHOMER<hr />
                          </div>
                          <div className="col-sm-6">
                            <b>Course Duration: </b>LIFETIME<hr />
                            <b>Entered Name: </b>{userData.userDetails.first_name} {userData.userDetails.last_name}<hr />
                            <b>Entered Email: </b>{userData.userDetails.email}<hr />
                          </div>
                        </div>
                        <hr />
                        {this.state.usernameAvailability === false &&
                          <div className="col-sm-6"><div className="alert alert-danger alert-dismissible fade show" role="alert">
                            <p>Username is already in used.</p>
                          </div></div>}
                        <div className="row">

                          <div className="col-sm-3">
                            <h6>Change Your Details:- </h6>
(If you want to change your details to remember it easily, you can change it from here)
                          </div>
                          <div className="col-sm-4">
                            <b>Username: </b><input type='text' name='username' id="username" value={this.state.username} onChange={this.onUsernameChange} /><hr />
                          </div>
                          <div className="col-sm-4">
                            <b>Password: </b><input type='password' minlength="6" name='password' id="pssword" value={this.state.password} onChange={this.onChange} /><hr />
                          </div>
                        </div>
                        <h4 className="mt-4">Payment Method</h4>
                        <div className="row detail-form">
                          <div className="col-md-12">
                            <span id="siteseal"></span>
                            {/* <div className="row" id="selectPaymentType">
                            <label className="check-container col-md-4">Payment with Paypal
                                <input type="radio" defaultChecked name="radio1" value="2" id="option2" className="paypal-radio paymentMethod"/>
                              <span className="checkmark"></span>
                            </label>
                            <label className="check-container col-md-4">Payment with Credit Card
                                <input type="radio" name="radio1" value="1" id="option1" className="credit-radio paymentMethod"/>
                              <span className="checkmark"></span>
                            </label>
                            
                        </div> */}

                            <div className="creditCardDiv" id="cardPaymentDiv1">
                              {clintId ? (
                                <>
                                  <DropIn options={{ authorization: clintId }}
                                    onInstance={instance => (this.instance = instance)}
                                  />
                                  <div className="">
                                    <div className="">
                                      <div className="checkbox">
                                        <input id="terms" type="checkbox" name="terms" value="terms" onChange={this.onCheckboxChange} checked={this.state.terms} />
                                        {this.validator.message('terms', this.state.terms, 'required')}
                                        <label htmlFor="terms">I have accept the <a href="/terms-of-services" target="_blank"> Terms and Conditions</a></label>
                                      </div>
                                    </div>
                                  </div>
                                  <div className="input-field col-md-6 text-right">
                                    <button onClick={this.buy.bind(this)} className="btn btn-lg waves-effect waves-light">Proceed to Payment</button>
                                  </div>
                                </>
                              )
                                : ''
                              }
                            </div>
                            {/* <div className="paypal" id="paypalPaymentDiv">
                            <div className="row"> 
                                <div className="col-md-12">
                                  <div className="input-field">
                                     <PaypalExpressBtn env={env} client={client} currency={currency} total={total} onError={onError} onSuccess={onSuccess} onCancel={onCancel} />
                                 </div>
                                </div>
                            </div>
                          </div> */}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </main>
        <DharmaFooter />
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  course: state.courses.item,
  braintree: state.courses.braintree,
  userDetails: state.userDetails.item,
  alert: state.alert,
});

export default connect(mapStateToProps, { buyCourseWithRegistration, getClintId, fetchCourseDetail })(CoursePayment);