import React,{Component} from 'react';
import { connect } from 'react-redux';
import {ToastsContainer, ToastsStore, ToastsContainerPosition} from 'react-toasts';
import { courseImagePath } from '../../../utils/helpers';
import {fetchCourseDetail} from '../../../actions/courseAction';

class CourseDetailInfo extends Component{
		
	componentDidMount() {
	    this.props.fetchCourseDetail(this.props.courseId, this.props.history)
	}

	render(){
		const {alert, courseDetail } = this.props;

		return(
			<>
			{alert.message && alert.type == 'success' &&
                  ToastsStore.success(alert.message) }
            {alert.message && alert.type == 'error' &&
                  ToastsStore.error(alert.message) } 
            <ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT}/>         
				<div className="card card-usercourse">
					    <div className="card-course-img">
					        <img src={courseImagePath(courseDetail.image)} className="img-fluid"/>
					    </div>
					    <div className="card-course-content ml-0">
					        <div className="info-div">
					            <div className="course-heading mb-0"><h6>{courseDetail.title}</h6>
					            <div className="course-rating">
					              <span className="course-price">${courseDetail.price}<small> USD</small></span>
					              <span className="course-duration"><img src="../../images/calendar.svg" /> { courseDetail.duration} { courseDetail.duration_type}</span>
					            </div>
					            </div>   
					            {/* <p className="my-2"><strong>{courseDetail.teacher_name}</strong></p>                     */}
					        </div> 
					        <div className="mt-5">
					        <p>{courseDetail.description}</p>
					        <div className="row buy-ul">
					          <div className="col-md-6">
					            <strong>Course Benefits</strong>
					            <ul className="course-benfits">
					              {courseDetail.benefits && courseDetail.benefits.map((benefit,index) => {
					                return(
					                    <li key={index}>{benefit}</li>
					                    );
					                })}
					            </ul>
					          </div>
					          <div className="col-md-6">
					            <strong>Course Offerings</strong>
					            <ul className="course-offer">
					             {courseDetail.offering && courseDetail.offering.map((offer,index) => {
					                    return(
					                      <li key={index}><i className={offer.class}></i> {offer.name}</li>
					                      );
					                  })}
					            </ul>
					          </div>
					        </div>
					        </div>
					    </div>           
					</div>
		        </>
			);
	}
}

const mapStateToProps = (state) =>({
  courseDetail: state.courses.item,
  alert: state.alert,
}); 

export default connect(mapStateToProps, { fetchCourseDetail })(CourseDetailInfo);