import React from 'react';

function VideoDetails(props) {
    let divClass = '';
    if(props.checkPreLaunch === true && props.video.pre_launch === '0'){
         divClass = 'videos-intabs disableData';
    }else{
         divClass = 'videos-intabs';
    }
    return (
        <div className={divClass}>
            <div className="row">
                <div className="col-md-4 col-lg-4">
                <div className="imgvid-div">
                    <img src={props.video.thumbnail} className="img-fluid image scale-on-hover moduleContentImage"/>
                    <img src={require('../../../assets/images/play-button.png')} className="img-fluid moduleContentImage play_button_image"/>
                   </div>
                    <div className='displayNonDiv'>
                        <video width="320" height="240" controls className='moduleContent'>
                            <source className="responsive-video" src={props.video.video} type="video/mp4" />
                        </video>
                    </div>
                </div>
                <div className="col-md-8 col-lg-8">
                    <h5 className="mb-3">{props.video.title}</h5>
                    <p>{props.video.description}</p> </div>
            </div>
        </div>
    );
}

export default VideoDetails;