import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { getLocalStorageAuth, logoutUser } from '../../utils/helpers';

class Header extends Component {

	constructor(props) {
		super(props);
		this.onDeleteClick = this.onDeleteClick.bind(this);
		const userData = getLocalStorageAuth();

		if (!userData) {
			this.state = {
				redirect: true,
				first_name: '',
				last_name: '',
				userType: '',
				userId: '',
			}
		} else {
			const firstName = userData.userDetails.first_name;
			const lastName = userData.userDetails.last_name;
			const userId = userData.userDetails.id;
			const userType = userData.userType;
			this.state = {
				redirect: false,
				first_name: firstName,
				last_name: lastName,
				userType: userType,
				userId: userId,
			}
		}
	}

	onDeleteClick() {
		logoutUser(this.props.history);
	}

	render() {
		const { alert } = this.props;
		if (this.state.redirect) {
			return (
				<Redirect to='/'/>
			);
		}
		let HeaderData = '';
		if (this.state.userType === 1) {
			HeaderData = <header>
				<nav className="navbar course-header fixed-top navbar-expand-lg scrolling-navbar" id="myTopnavbar">
					<div className="container">
						<div className="navbar-brand">
							<div className="logo-image">
								<Link to="/user/course-details/MQ==" className="lg-w">
									<img src={require('../../assets/images/logo-sattva-white.png')} alt="" />
								</Link>
								<Link to="/user/course-details/MQ==" className="lg-c">
									<img className="logo-img" src={require('../../assets/images/logo-sattva.png')} alt="Sattva Connect" />
								</Link>
							</div>
						</div>
						<button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbar" aria-controls="navbar" aria-expanded="false" aria-label="Toggle navigation">
							<span className="navbar-toggler-icon"></span>
						</button>
						<div className="collapse navbar-collapse" id="navbar">
							<ul className="navbar-nav ml-auto">
								<li className="nav-item">
									<Link to="/user/account-details" className="nav-link">Account Details</Link>
								</li>
								<li className="nav-item">
									<Link to="/user/contact-us" className="nav-link">Contact</Link>
								</li>
								<li className="nav-item">
									<a className="nav-link btn signup-btn waves-effect waves-light" onClick={this.onDeleteClick}>Logout</a>
								</li>
							</ul>
						</div>
					</div>
				</nav>
			</header>
		} else {
			HeaderData = <header>
				<nav className="navbar course-header fixed-top navbar-expand-lg scrolling-navbar" id="myTopnavbar">
					<div className="container">
						<div className="navbar-brand">
							<div className="logo-image">
								<Link to="/user/course-info/MQ==" className="lg-w">
									<img src={require('../../assets/images/logo-sattva-white.png')} alt="" />
								</Link>
								<Link to="/user/course-info/MQ==" className="lg-c">
									<img className="logo-img" src={require('../../assets/images/logo-sattva.png')} alt="Sattva Connect" />
								</Link>
							</div>
						</div>
						<button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbar" aria-controls="navbar" aria-expanded="false" aria-label="Toggle navigation">
							<span className="navbar-toggler-icon"></span>
						</button>
						<div className="collapse navbar-collapse" id="navbar">
							<ul className="navbar-nav ml-auto">
								<li className="nav-item">
									<Link to="/user/account-details" className="nav-link">Account Details</Link>
								</li>
								<li className="nav-item">
									<Link to="/user/contact-us" className="nav-link waves-effect waves-light">
										<span>Contact Us</span>
									</Link>
								</li>
								{this.props.shoBuyBtn ?
									<li className="nav-item">
										<Link to={'/payment/MQ==/' + btoa(this.state.userId)} className="nav-link btn signup-btn">Buy Now</Link>
									</li> : ''
								}
								<li className="nav-item">
									<a className="nav-link btn signup-btn waves-effect waves-light" onClick={this.onDeleteClick} name="Submit">Logout</a>
								</li>
							</ul>
						</div>
					</div>
				</nav>
			</header>
		}
		return (
			<>
				{alert && alert.isloading &&
					<div className="preloader-background">
						<div className="big sattva_loader active">
							<img src={require('../../assets/images/loader.png')} />
						</div>
					</div>
				}
				{HeaderData}
			</>
		);
	}
}

const mapStateToProps = (state) => ({
	alert: state.alert,
});

export default connect(mapStateToProps, null)(Header);