import React,{Component} from 'react';
import { connect } from 'react-redux';
import {changeUserPassword} from '../../actions/userAction';
import SimpleReactValidator from 'simple-react-validator';

class UserChangePasswordForm extends Component{

	constructor(props){
		super(props);
	    this.validator = new SimpleReactValidator();

        const  max = 9;
        const rand1 = Math.floor(Math.random(1) * Math.floor(max));
        const rand2 = Math.floor(Math.random(1) * Math.floor(max));
		    this.state = {
		  		password : '',
          confirmPassword: '',
          securityVal: '',
          security: true,
          rand1: rand1,
          rand2: rand2,
          currectSecurity: rand1+rand2,
          passwordStatus:true,
	 	   } 
	   
	  this.onChange = this.onChange.bind(this);
	  this.onSubmit = this.onSubmit.bind(this);
    this.resetForm = this.resetForm.bind(this);
    this.onRecaptchaChange = this.onRecaptchaChange.bind(this);
	}

	componentDidMount() {
    window.scrollTo(0, 0);
	}

	onChange(e){
		this.setState({ [e.target.name] : e.target.value });
	}

  onRecaptchaChange(e){
    this.setState({  securityVal: e.target.value });
    let currentVal = Number(e.target.value);
    const currectVal = this.state.currectSecurity;
    if(currentVal !== currectVal){
      this.setState({security: false});
    }else{
       this.setState({security: true});
    }
  }

	onSubmit(e){
		e.preventDefault();
    if (!this.validator.allValid()) {
          this.validator.showMessages();
          this.forceUpdate();
          return false;
      } 
      if (!this.state.security) {
         return false;
      }
      if (this.state.password !== this.state.confirmPassword ) {
        this.setState({passwordStatus: false});
        return false;
      }else{
        this.setState({passwordStatus: true});
      }
	
    	const userDetails = {
    		id : this.props.userId,
			  password : this.state.password,
		  }	
     this.props.changeUserPassword(userDetails);
     window.scrollTo(0, 0);
     this.resetForm();
	}

  resetForm(){
    this.validator.hideMessages();
    document.getElementById("changePasswordForm").reset();
    this.setState({
        password : '',
        confirmPassword: '',
        securityVal: '',
        security: true,
        passwordStatus:true,
    });
  }

	render(){
		return(
			<>   
      <form onSubmit={this.onSubmit} id="changePasswordForm">
          <div className="row detail-form mt-4">
          
            
             <div className="input-field col-md-6">
               <label id="password-lbl" for="password" className="required">Enter New Password<span className="star">&nbsp;*</span></label>
               <input type="password" name="password" id="password"  value={this.state.password} onChange={this.onChange}/>
               {this.validator.message('password', this.state.password, 'required|min:6')}
             </div>
             <div className="input-field col-md-6">
               <label id="cpassword-lbl" for="cpassword" className="required">Confirm New Password<span className="star">&nbsp;*</span></label>
               <input type="password" name="confirmPassword" id="cpassword"  value={this.state.confirmPassword} onChange={this.onChange}/>
               {this.validator.message('confirmPassword', this.state.confirmPassword, 'required|min:6')}
             </div>
             <div className="input-field col-md-12">
             {this.state.passwordStatus === false &&
                  <div className="alert alert-danger alert-dismissible fade show resetpass-error" role="alert">
             <p>Password does not match</p>
            </div>} 
             </div>
              <div className="input-field col-md-6">
                  <div className="security-check">
                    <div className="input-group-prepend">
                      <span>{this.state.rand1} + {this.state.rand2}</span>
                    </div>
                    <div className="security-addon">
                        <label id="security-lbl" for="security" className="required">Security Check <span className="star">&nbsp;*</span></label>
                        <input type="number" name="securityVal" id="security"  value={this.state.securityVal} onChange={this.onRecaptchaChange}/>
                        {this.validator.message('Recaptcha', this.state.securityVal, 'required')}
                    </div>
                  </div>
             </div>
             <div className="input-field col-md-6">
             {this.state.security === false &&
                  <div className="alert alert-danger alert-dismissible fade show resetpass-error" role="alert">
             <p>Incorrect Recaptcha</p>
            </div>}
             </div>
              
          </div><div className="text-right">
              <button type="submit" className="btn btn-lg waves-effect waves-light">Change Password</button>
          </div>
      </form>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {changeUserPassword})(UserChangePasswordForm);