import React,{Component} from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import {ToastsContainer, ToastsStore, ToastsContainerPosition} from 'react-toasts';
import axios from 'axios';
import { apiRoute, getApiHeader } from '../../../utils/helpers';
import {setLoadingTrue, clearAllAlerts} from '../../../actions/userAction';
import CourseServices from '../../../services/courseServices';
import SimpleReactValidator from 'simple-react-validator';
import CountryOptions from '../common/CountryOptions';
import PhoneCodeOptions from '../common/PhoneCodeOptions';

class BuyCourseForm extends Component{
		  
  constructor(props){
    super(props);
      this.validator = new SimpleReactValidator();
      this.state = {
        username : '',
        password : '',
        confirmPassword: '',
        firstName: '',
        lastName: '',
        email : '',
        address : '',
        city: '',
        country: '',
        countryCode: '',
        phone: '',
        zip: '',
        terms: '',
        affiliateUrlId: this.props.affiliateUrlId,
        courseId: this.props.courseId,
        passwordStatus:true,
        redirectToPayment:false,
        usernameAvailability:true,
        emailAvailability:true,
        userId: '',
        purchaseId: '',
    } 
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
     this.onCheckboxChange = this.onCheckboxChange.bind(this);
    this.onUsernameChange = this.onUsernameChange.bind(this);
    this.onEmailChange = this.onEmailChange.bind(this);
    this.onCountryChange = this.onCountryChange.bind(this);
  }

  onChange(e){
    this.setState({ [e.target.name] : e.target.value });
  }

  onCountryChange(e){
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index]
    var phoneCode =  optionElement.getAttribute('phonecode');
    this.setState({ country : e.target.value });
    this.setState({ countryCode : phoneCode });
  }
   onCheckboxChange(e){
    this.setState({ terms : e.target.checked });
  }

  onUsernameChange(e){
     const userName = e.target.value;
     this.setState({ username : userName });
     CourseServices.checkUsernameAvailability(userName)
     .then(res =>{
        if(res.data.status !== true){
          this.setState({ usernameAvailability : false });
        }else{
           this.setState({ usernameAvailability : true });
        }
    })
     
  }

  onEmailChange(e){
     const email = e.target.value;
     this.setState({ email : email });
     CourseServices.checkEmailAvailability(email)
     .then(res =>{
        if(res.data.status !== true){
          this.setState({ emailAvailability : false });
        }else{
           this.setState({ emailAvailability : true });
        }
    })
     
  }

  onSubmit(e){
    e.preventDefault();
    const { password, confirmPassword } = this.state;
    if (!this.validator.allValid()) {
          this.validator.showMessages();
          this.forceUpdate();
          return false;
      } 

    if (password !== confirmPassword) {
         this.setState({ passwordStatus : false }); 
        return false;
    } 
    if (this.state.usernameAvailability === false) {
        return false;
    }
    if (this.state.emailAvailability === false) {
        return false;
    }

    const userData = {
        username: this.state.username,
        password: this.state.password,
        confirmPassword: this.state.confirmPassword,
        firstName: this.state.firstName,
        lastName: this.state.lastName,
        email : this.state.email,
        address : this.state.address,
        city: this.state.city,
        country: this.state.country,
        countryCode: this.state.countryCode,
        phone: this.state.phone,
        zip: this.state.zip,
        userType: 1,
        courseId: this.state.courseId,
        affiliateUrlId: this.state.affiliateUrlId,
    }
    
    this.props.setLoadingTrue();
    const requestOptions = {
			headers : getApiHeader()
		};
		axios.post(apiRoute('new-course-user-registration'),userData, requestOptions)
		.then(res =>{
      this.setState({
        purchaseId: res.data.id,
        redirectToPayment:true,
       })
      this.props.clearAllAlerts();
      })
      .catch(error => {
        if (error.response && error.response.status) {
            let errorData = error.response.data;
            ToastsStore.error(errorData) 
        } else {
          ToastsStore.error('Something went wrong. Please try again') 
        }
        
    });
      
  }

	render(){
   
    return(
      <>
      {this.state.redirectToPayment &&
        <Redirect to={'/payment/'+this.state.courseId +'/'+btoa(this.state.purchaseId)}/>
      }
      <ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT}/>
			<div className="card p-4 d-block">
            <div className="customer-support p-0">
                <form  onSubmit={this.onSubmit} id='buyCourseForm' autoComplete="off" className="form form-horizontal">
                    <h4>Your Details</h4>
                    {this.state.emailAvailability === false &&
                      <div className="col-sm-6">
                      <div className="alert alert-danger alert-dismissible fade show" role="alert">
                             <p>Email is already in used.</p>
                            </div></div>}
                    {this.state.usernameAvailability === false &&
                       <div className="col-sm-6"><div className="alert alert-danger alert-dismissible fade show" role="alert">
                       <p>Username is already in used.</p>
                      </div></div>}
                      {this.state.passwordStatus === false &&
                       <div className="col-sm-6"><div className="alert alert-danger alert-dismissible fade show" role="alert">
                       <p>Password does not match.</p>
                      </div></div>}  
                    <div className="row detail-form">
                     <div className="input-field col-md-6">
                         <label id="username-lbl" htmlFor="username" className=" required">User Name<span className="star">&#160;*</span></label>
                         <input type="text" name="username" id="username"  value={this.state.username} onChange={this.onUsernameChange}/>
                          {this.validator.message('username', this.state.username, 'required')}
                       </div>
                       <div className="input-field col-md-6">
                         <label id="password-lbl" fohtmlfor="password" className=" required">Password<span className="star">&#160;*</span></label>
                         <input type="password" name="password" id="password" value={this.state.password} onChange={this.onChange}/>
                          {this.validator.message('password', this.state.password, 'required|min:6')}
                       </div>
                       <div className="input-field col-md-6">
                         <label id="confirmPassword-lbl" htmlFor="confirmPassword" className=" required">Confirm Password<span className="star">&#160;*</span></label>
                         <input type="password" name="confirmPassword" id="confirmPassword" value={this.state.confirmPassword} onChange={this.onChange}/>
                        {this.validator.message('confirmPassword', this.state.confirmPassword, 'required|min:6')}
                       </div>
                      <div className="input-field col-md-6">
                        <label id="firstName-lbl" htmlFor="firstName" className=" required">First Name<span className="star">&#160;*</span></label>
                        <input type="text" name="firstName" id="firstName" value={this.state.firstName} onChange={this.onChange}/>
                        {this.validator.message('firstName', this.state.firstName, 'required|alpha|max:50')}
                      </div>
                      <div className="input-field col-md-6">
                        <label id="lastName-lbl" htmlFor="lastName" className="required">Last Name<span className="star">&#160;*</span></label>
                        <input type="text" name="lastName" id="lastName" value={this.state.lastName} onChange={this.onChange}/>
                        {this.validator.message('lastName', this.state.lastName, 'required|alpha|max:50')}
                      </div>
                      <div className="input-field col-md-6">
                        <label id="email-lbl" htmlFor="email" className=" requir">Email ID<span className="star">&#160;*</span></label>
                        <input type="text" name="email" id="email" value={this.state.email} onChange={this.onEmailChange}/>
                      {this.validator.message('email', this.state.email, 'required|email')}
                      </div>
                      <div className="col-md-6 input-field ">
                         <label id="address-lbl" htmlFor="address" className=" required">Address<span className="star">&#160;*</span></label>
                         <input type="text" name="address" id="address"  value={this.state.address} onChange={this.onChange}/>
                         {this.validator.message('address', this.state.address, 'required|max:1200')}
                      </div>
                      <div className="input-field col-md-6">
                      <label id="city-lbl" htmlFor="city" className="required">City<span className="star">&#160;*</span></label>
                        <input type="text" name="city" id="city"  value={this.state.city} onChange={this.onChange}/>
                        {this.validator.message('city', this.state.city, 'required|max:50')}
                      </div>
                      <div className="input-field col-md-6">
                        <select id="country" name="country" onChange={this.onCountryChange}>
                         <option defaultValue>Country *</option>
                          <CountryOptions/>
                        </select>
                        {this.validator.message('country', this.state.country, 'required')}
                      </div>
                      <div className="input-field col-md-6">
                        <select id="countryCode" name="countryCode" value={this.state.countryCode} onChange={this.onChange}>
                        <option>Dial Code(+) *</option>
                        <PhoneCodeOptions/>
                         </select>
                        {this.validator.message('countryCode', this.state.countryCode, 'required')}
                      </div>
                       <div className="col-md-6 input-field ">
                         <label id="phone-lbl" htmlFor="phone" className=" required">Phone Number<span className="star">&#160;*</span></label>
                         <input type="number" name="phone" id="phone" value={this.state.phone} onChange={this.onChange}/>
                         {this.validator.message('phone', this.state.phone, 'required|min:10|phone')}
                      </div>
                      <div className="input-field col-md-6">
                        <label id="zip-lbl" fohtmlfor="zip" className=" required">Zip Code<span className="star">&#160;*</span></label>
                        <input type="number" name="zip" id="zip" value={this.state.zip} onChange={this.onChange}/>
                        {this.validator.message('zip', this.state.zip, 'required|numeric')}
                      </div>
                      <div className="">
                            <div className="">
                              <div className="checkbox">
                                  <input id="terms" type="checkbox" name="terms" value="terms" onChange={this.onCheckboxChange} checked={this.state.terms}/>
                                    {this.validator.message('terms', this.state.terms, 'required')}
                                    <label htmlFor="terms">I have accept the <Link to="/terms-of-services" target="_blank"> Terms and Conditions</Link></label>
                                </div>
                            </div>
                        </div>
                    </div>
                     <div className="input-field col-md-6 text-right">
                          <button type="submit" className="btn btn-lg">Proceed to Payment</button>
                      </div>
                    </form>
                </div>
       		 </div>
           </>
			);
	}
}

const mapStateToProps = (state) =>({
  course: state.courses.item,
});
export default connect(mapStateToProps, { setLoadingTrue, clearAllAlerts })(BuyCourseForm);