import React,{Component} from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import RoadToDharmaHeader from '../../components/frontend/common/RoadToDharmaHeader';
import {sendForgotPasswordMail} from '../../actions/userAction';
import UserChangePasswordForm from '../../components/user/UserChangePasswordForm';

class ResetPassword extends Component{

	componentDidMount() {
    	window.scrollTo(0, 0);
	}

	render(){
		const {alert} = this.props;
		return(
			<>
			 {alert && alert.isloading &&
			  <div className="preloader-background">
			  	<div class="big sattva_loader active">
				      <img src={require('../../assets/images/loader.png')}/>
				</div>
			  </div>
			}    
			<RoadToDharmaHeader/> 
			<main className="ResetPassword">
			    <section className="sec">
			      <div className="container">
			        <div className="row justify-content-center">
			          <div className="col-md-7 col-sm-12">
				           {alert.message && alert.type == 'error' &&
				              <div className="alert alert-danger alert-dismissible fade show" role="alert">
								 {alert.message}
								</div>}
				            {alert.message && alert.type == 'success' &&
				                <div className="alert alert-success alert-dismissible fade show" role="alert">
								  {alert.message}
								</div> }

			            <div className="card subscription-card customer-support">
			            <h4>Reset Password</h4>	
			                 <UserChangePasswordForm userId={this.props.match.params.userId}/>
			                 <div className="login-links">
			                    <div className="input-field">
			                      <Link to='/userlogin'>Go to Login.</Link>
			                    </div>
			                </div>
			            </div>
			          </div>
			        </div>             
			     </div>
			    </section>
			  </main>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, { sendForgotPasswordMail })(ResetPassword);