import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { getLocalStorageAuth } from '../../../utils/helpers';

class RoadToDharmaHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      redirect: false,
      auth: [],
    };
  }
  componentDidMount() {
    const userData = getLocalStorageAuth();

    if (userData) {
      this.setState({
        redirect: true,
        auth: userData,
      });
    }
  }

  render() {
    const { alert } = this.props;
    if (this.state.redirect) {
      if (this.state.auth.userType === 1) {
        return <Redirect to='/user/course-details/MQ==' />;
      } else {
        return <Redirect to='/user/course-info/MQ==' />;
      }
    }
    return (
      <>
        {alert && alert.isloading && (
          <div className='preloader-background'>
            <div className='big sattva_loader active'>
              <img src={require('../../../assets/images/loader.png')} />
            </div>
          </div>
        )}
        <header>
          <nav className='navbar fixed-top navbar-expand-lg scrolling-navbar'>
            <div className='navbar-brand'>
              <div className='logo-image'>
                <Link to='/' className='lg-w'>
                  <img
                    src={require('../../../assets/images/logo-sattva-white.png')}
                    alt=''
                  />
                </Link>
                <Link to='/' className='lg-c'>
                  <img
                    className='logo-img'
                    src={require('../../../assets/images/logo-sattva.png')}
                    alt='Sattva Connect'
                  />
                </Link>
              </div>
            </div>
            <button
              className='navbar-toggler collapsed'
              type='button'
              data-toggle='collapse'
              data-target='#navbar'
              aria-controls='navbar'
              aria-expanded='false'
              aria-label='Toggle navigation'
            >
              <span className='navbar-toggler-icon'></span>
            </button>
            <div className='collapse navbar-collapse' id='navbar'>
              <ul className='navbar-nav ml-auto'>
                {/* <li className="nav-item">
				                	<Link to="/about-us" className="nav-link">
				                    About Us
				                    </Link>
				                </li>
				                <li className="nav-item">
				               		<Link to="/courses" className="nav-link">
				                    Courses
				                    </Link> 
									<Link to="/course_details/MjM=" className="nav-link">
				                    Courses
				                    </Link>
				                </li> */}
                <li className='nav-item'>
                  <Link to='/contact-us' className='nav-link'>
                    Contact
                  </Link>
                </li>
                <li className='nav-item'>
                  <Link to='/userlogin' className='nav-link btn signup-btn'>
                    Login
                  </Link>
                </li>
              </ul>
            </div>
          </nav>
        </header>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  alert: state.alert,
});

export default connect(mapStateToProps, null)(RoadToDharmaHeader);
