import React,{Component} from 'react';
import { connect } from 'react-redux';
import DropIn from "braintree-web-drop-in-react";
import PaypalExpressBtn from 'react-paypal-express-checkout';
import Header from '../../components/user/Header';
import {fetchCourseDetail, getClintId} from '../../actions/courseAction';
import {buyNewCourse} from '../../actions/userAction';
import { getLocalStorageAuth } from '../../utils/helpers';

class CoursePayment extends Component{
		
    constructor(props){
      super(props);
        this.state = {
          clientToken: false,
          error:false,
          errorMessage:'',
      } 
  } 

  async componentDidMount() {
    window.scrollTo(0, 0);
     this.props.getClintId(this.props.history);
     this.props.fetchCourseDetail(this.props.match.params.courseId, this.props.history) 

  } 
   async buy() {
    const { nonce } = await this.instance.requestPaymentMethod();
    //await fetch(`https://betasite.online/sattva-courses/api/make-card-payment/${nonce}`);
    this.buyNewCourse(nonce, 'Credit Card')
  }
  buyNewCourse(paymentDetails, type){
    const auth = getLocalStorageAuth();
    console.log(auth.userDetails.id);
    const data = {
      userDetail: auth.userDetails.id,
      courseDetail: this.props.course,
      paymentDetail: paymentDetails,
      paymentType: type,
    }
    this.props.buyNewCourse(data, this.props.history)
  }
	render(){

  const {course, userDetails, alert} = this.props;
  const onSuccess = (payment) => {
      console.log("The payment was succeeded!");
      this.buyNewCourse(payment, 'PayPal')
       }
 
    const onCancel = (data) => {
       console.log("Fail");
       this.setState({
          error:true,
          errorMessage:'Something went wrong. Please try again',
       });
        }

    const onError = (err) => {
       console.log("Fail");
       this.setState({
          error:true,
          errorMessage:'Something went wrong. Please try again',
       });
       }

    let env = 'sandbox'; // you can set here to 'production' for production
    let currency = 'USD'; // or you can set this value from your props or state
    let total = course.price; // same as above, this is the total amount (based on currency) to be paid by using Paypal express checkout
  
    const client = {
        sandbox: 'AdL1rB3R2-vlQUStB5-4G0SZiPlx-giWIOQSL2kCMB4YHbz_F4DC6zGEg6gKUhPIIqFtS42SfV5-dMWD',
        production: 'EFcgJ7xNP2VPGsk0_Q2wvI8cVHd8xcFiN9PU3uVU-xkXeYbij7pmjLSCUVDLnswtkLhZEwsuJOYfD2dt',
     }
    const clintId = this.props.braintree;
   return(
      <>
      <Header  history={this.props.history}/>
      <main>
      <section className="sec">
      <div className="container">
        <div className="row">
          <div className="col-xl-8 col-lg-8 col-md-8 col-sm-12">
            <h4>Payment Summry</h4>
             {this.state.error  &&
                    <div className="alert alert-danger alert-dismissible fade show" role="alert">
               {this.state.errorMessage}
               <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
              </div>}
            {alert.message && alert.type == 'error' &&
                    <div className="alert alert-danger alert-dismissible fade show" role="alert">
               {alert.message}
               <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
              </div>}
                  {alert.message && alert.type == 'success' &&
                      <div className="alert alert-success alert-dismissible fade show" role="alert">
                {alert.message}
                  <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                  </button>
              </div> } 
              <div className="contact-form">
                  <div className="card">
                    <div className="customer-support">
                     <legend></legend>
                     <b>Course Name: </b>{ course.title}<hr/>
                     <b>Course Price: </b>${ course.price}<hr/>
                     <b>Teacher Name: </b>{ course.teacher_name}<hr/>
                     <b>Course Duration: </b>{ course.duration} {course.duration_type}<hr/>
                     <h4 class="mt-4">Payment Method</h4>
                     <div className="row detail-form">
                      <div className="col-md-12">
                        <div className="row" id="selectPaymentType">
                            <label className="check-container col-md-4">Payment with Paypal
                                <input type="radio" checked name="radio1" value="2" id="option2" className="paypal-radio paymentMethod"/>
                              <span className="checkmark"></span>
                            </label>
                            <label className="check-container col-md-4">Payment with Credit Card
                                <input type="radio" name="radio1" value="1" id="option1" className="credit-radio paymentMethod"/>
                              <span className="checkmark"></span>
                            </label>
                            
                        </div>
                        <div className="creditCardDiv" id="cardPaymentDiv">
                          { clintId ? (
                              <>
                               <DropIn options={{ authorization: clintId }}
                                 onInstance={instance => (this.instance = instance)}
                                />
                                <div className="input-field col-md-6 text-right">
                                    <button  onClick={this.buy.bind(this)} className="btn btn-lg waves-effect waves-light">Proceed to Payment</button>
                                </div>
                                </>
                              )
                              : ''
                            }
                          </div>
                          <div className="paypal" id="paypalPaymentDiv">
                            <div className="row"> 
                                <div className="col-md-12">
                                  <div className="input-field">
                                     <PaypalExpressBtn env={env} client={client} currency={currency} total={total} onError={onError} onSuccess={onSuccess} onCancel={onCancel} />
                                 </div>
                                </div>
                            </div>
                          </div>
                      </div>                  
                    </div>
                   </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>  
      </main>
      </>            
      );
	}
}

const mapStateToProps = (state) =>({
  course: state.courses.item,
  braintree: state.courses.braintree,
  userDetails: state.userDetails.item,
  alert: state.alert,
});

const mapActionToProps = ({
  fetchCourseDetail: fetchCourseDetail,
  getClintId: getClintId,
  buyNewCourse: buyNewCourse,
});

export default connect(mapStateToProps, mapActionToProps)(CoursePayment);