import React,{Component} from 'react';
import { connect } from 'react-redux';
import SimpleReactValidator from 'simple-react-validator';
import Header from '../../../components/user/Header';
import {sendContactMail, clearAllAlerts} from '../../../actions/userAction';

class ContactUsForm extends Component{

	constructor(props){
		super(props);
		this.props.clearAllAlerts();
	    this.validator = new SimpleReactValidator();
	    this.state = {
	  		email : '',
	  		subject : '',
	  		message: '',
	  		name: '',
	  }	
	  this.onChange = this.onChange.bind(this);
	  this.onSubmit = this.onSubmit.bind(this);
	}


	componentDidMount() {
    window.scrollTo(0, 0);
	}


	onChange(e){
		this.setState({ [e.target.name] : e.target.value });
	}

	onSubmit(e){
		e.preventDefault();
		if (!this.validator.allValid()) {
            this.validator.showMessages();
            this.forceUpdate();
            return false;
        }	
    	const userDetails = {
			email: this.state.email,
			subject: this.state.subject,
			message: this.state.message,
			name: this.state.name,
		}	
        this.props.sendContactMail(userDetails)
        this.validator.hideMessages();
		document.getElementById("contact-form").reset();
		this.setState({
			email: '',
			subject: '',
			message: '',
			name: '',
		});
 		window.scrollTo(0, 0);
	}


	render(){
		const {alert} = this.props;
		return(
			<>
			 {/* <main className="admin-content"> */}
			 <main className="contact-us">
			    <section className="sec">
			      <div className="sec pb-0 container">
					<div className="col-md-12">
			        <div className="row">
			          <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12">
			            <h4>Contact Us</h4>
			             {alert.message && alert.type == 'error' &&
			              <div className="alert alert-danger alert-dismissible fade show" role="alert">
							 {alert.message}
							</div>}
			            {alert.message && alert.type == 'success' &&
			                <div className="alert alert-success alert-dismissible fade show" role="alert">
							  {alert.message}
							</div> } 
			            <div className="contact-form">
			               <form id="contact-form" className="form-validate form-horizontal" onSubmit={this.onSubmit}>
			                  <div className="card">
			                    <div className="customer-support">
			                     <legend></legend>
			                     <div className="input-field">
			                        <label id="jform_contact_name-lbl" for="jform_contact_name" className="hasTooltip required control-label" title="<strong>Name</strong><br />Your name">Name<span className="star">&#160;*</span></label> 
			                        <input type="text" name="name" id="jform_contact_name" size="30" required aria-required="true"  value={this.state.name} onChange={this.onChange}/>     
			                     	{this.validator.message('name', this.state.name, 'required')}
			                     </div>
			                     <div className="input-field">
			                        <label id="jform_contact_email-lbl" for="jform_contact_email" className="hasTooltip required control-label" title="<strong>Email</strong><br />Email for contact">Email<span className="star">&#160;*</span></label>    
			                        <input type="email" name="email" className="validate-email form-control" id="jform_contact_email"  size="30" required aria-required="true"  value={this.state.email} onChange={this.onChange}/>      
			                     	{this.validator.message('email', this.state.email, 'required|email')}
			                     </div>
			                     <div className="input-field">
			                        <label id="jform_contact_emailmsg-lbl" for="jform_contact_emailmsg" className="hasTooltip required control-label" title="<strong>Subject</strong><br />Enter the subject of your message here .">Subject<span className="star">&#160;*</span></label>     
			                        <input type="text" name="subject" id="jform_contact_emailmsg"size="60" required aria-required="true"  value={this.state.subject} onChange={this.onChange}/>      
			                     	{this.validator.message('subject', this.state.subject, 'required')}
			                     </div>
			                     <div className="input-field">
			                        <label id="jform_contact_message-lbl" for="jform_contact_message" className="hasTooltip required control-label" title="<strong>Message</strong><br />Enter your message here.">Message<span className="star">&#160;*</span></label>
			                        <textarea name="message" id="jform_contact_message" cols="50" rows="10" className="materialize-textarea form-control" required aria-required="true" onChange={this.onChange}></textarea>
			                     	{this.validator.message('message', this.state.message, 'required')}
			                     </div>
			                     <div className="input-field">
			                      <button className="btn btn-lg" type="submit">Send Email</button>
			                     </div>
			                   </div>
			                  </div>
			               </form>
			            </div>
			          </div>
			          <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12">
			            <h4>Miscellaneous Information</h4>
			              <div className="card">
			                 <div className="customer-support">
			                    <span className="contact-misc">
			                       <div className="address_information">
			                          <h6>For more information on Anand Mehrotra and Sattva Yoga, including:<br /><br /></h6>
			                          <p>*Anand’s scheduled USA tour <br />*India retreats and Teacher Training<br />*Information about staying at the Sattva Centre in Rishikesh India<br />*Sattva Studios Worldwide Please goto <a href="http://www.mysattva.com/">www.mysattva.com</a><a href="http://www.mysattva.com/" target="_blank"><br /><br /></a></p>
			                          <h6>For membership questions and general inquiries<br /><br /></h6>
			                          <p>Phone: (+91-8469451-45456)</p>
			                          <p>Please email: <a href="mailto:info@sattvaconnect.com">info@sattvaconnect.com</a><a href="mailto:info@sattvaconnect.com"><br /><br /></a></p>
			                          <h6>Visit Us<br /><br /></h6>
			                          <p>Sattva Retreat, Mohan Chatti, Rishikesh, Himalayas - India.<br /><br /></p>
			                          <h6>For media professionals seeking events, interviews, and social media, please email:<br /><br /></h6>
			                          <p><a href="mailto:media@sattvaconnect.com">media@sattvaconnect.com</a></p>
			                       </div>
			                    </span>
			                 </div>
			              </div>
			          </div>
			        </div>
					</div>
				  </div>
			    </section>  
			  </main>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {sendContactMail, clearAllAlerts})(ContactUsForm);