import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import cookie from 'react-cookies';
import CourseServices from '../../services/courseServices';
import SimpleReactValidator from 'simple-react-validator';
import RoadToDharmaHeader from '../../components/frontend/common/RoadToDharmaHeader';
import { changeUserPassword, clearAllAlerts } from '../../actions/userAction';

class ForgotPassword extends Component {

	constructor(props) {
		super(props);
		this.props.clearAllAlerts();
		this.validator = new SimpleReactValidator();
		const max = 9;
		const rand1 = Math.floor(Math.random(1) * Math.floor(max));
		const rand2 = Math.floor(Math.random(1) * Math.floor(max));
		this.state = {
			emailAvailability: true,
			email: '',
			userId: '',
			showError: false,
			errorMessage: '',
			showSuccess: false,
			successMessage: '',
			password: '',
			confirmPassword: '',
			securityVal: '',
			security: true,
			rand1: rand1,
			rand2: rand2,
			currectSecurity: rand1 + rand2,
			passwordStatus: true,
		}
		this.onChange = this.onChange.bind(this);
		this.onEmailChange = this.onEmailChange.bind(this);
		this.onSubmit = this.onSubmit.bind(this);
		this.resetForm = this.resetForm.bind(this);
		this.onRecaptchaChange = this.onRecaptchaChange.bind(this);
	}

	componentDidMount() {
		window.scrollTo(0, 0);
	}

	onChange(e){
		this.setState({ [e.target.name] : e.target.value });
	}

	onEmailChange(e) {
		const email = e.target.value;
		this.setState({ email: email });
		if(typeof email !== "undefined"){
            let lastAtPos = email.lastIndexOf('@');
            let lastDotPos = email.lastIndexOf('.');
 
            if ((lastAtPos < lastDotPos && lastAtPos > 0 && email.indexOf('@@') == -1 && lastDotPos > 2 && (email.length - lastDotPos) > 2)) {
               
		CourseServices.checkEmailAvailability(email)
			.then(res => {
				if (res.data.status !== true) {
					this.setState({ emailAvailability: true });
					this.setState({ userId: btoa(res.data.user.id) });
					this.setState({ showError: false });
					this.setState({ errorMessage: '' });
				} else {
					this.setState({ emailAvailability: false });
					this.setState({ showError: true });
					this.setState({ errorMessage: 'Email does not exist.' });
				}
			})
		}else{
			this.setState({ emailAvailability: true });
			this.setState({ showError: false });
			this.setState({ errorMessage: '' });
        } 
	}
}

	onRecaptchaChange(e){
		this.setState({  securityVal: e.target.value });
		let currentVal = Number(e.target.value);
		const currectVal = this.state.currectSecurity;
		if(currentVal !== currectVal){
		  this.setState({security: false});
		}else{
		   this.setState({security: true});
		}
	  }

	onSubmit(e) {
		e.preventDefault();
		this.setState({ showError: false });
		this.setState({ showSuccess: false });
		if (!this.validator.allValid()) {
			this.validator.showMessages();
			this.forceUpdate();
			return false;
		}
		if (!this.state.security) {
			return false;
		 }
		 if (this.state.password !== this.state.confirmPassword ) {
		   this.setState({passwordStatus: false});
		   return false;
		 }else{
		   this.setState({passwordStatus: true});
		 }
		if (!this.state.emailAvailability) {
			this.setState({ showError: true });
			this.setState({ errorMessage: 'Email does not exist.' });
			return false;
		}
		const userDetails = {
    		id : this.state.userId,
			password : this.state.password,
		  }	
		this.props.changeUserPassword(userDetails)
		cookie.save('password', this.state.password, { path: '/userlogin' });
		this.resetForm();
		window.scrollTo(0, 0);
	}

	resetForm(){
		this.validator.hideMessages();
		document.getElementById("changePasswordForm").reset();
		this.setState({
			password : '',
			email : '',
			confirmPassword: '',
			securityVal: '',
			security: true,
			passwordStatus:true,
		});
	  }
	render() {
		const { alert } = this.props;
		return (
			<>
				{alert && alert.isloading &&
					<div className="preloader-background">
						<div class="big sattva_loader active">
							<img src={require('../../assets/images/loader.png')} />
						</div>
					</div>
				}
				<RoadToDharmaHeader />
				<main className="admin-content">
					<section className="sec">
						<div className="container">
							<div className="row justify-content-center">
								<div className="col-md-7 col-sm-12">
									{alert.message && alert.type === 'error' &&
										<div className="alert alert-danger alert-dismissible fade show" role="alert">
											{alert.message}

										</div>}
									{alert.message && alert.type === 'success' &&
										<div className="alert alert-success alert-dismissible fade show" role="alert">
											{alert.message}

										</div>}
									{this.state.showError === true &&
										<div className="col-sm-6">
											<div className="alert alert-danger alert-dismissible fade show" role="alert">
												<p>{this.state.errorMessage}</p>
											</div></div>}
									{this.state.showSuccess === true &&
										<div className="col-sm-6">
											<div className="alert alert-success alert-dismissible fade show" role="alert">
												<p>{this.state.successMessage}</p>
											</div></div>}
									<div className="card subscription-card customer-support">
										<form autocomplete="off" id="changePasswordForm" className="form form-horizontal" onSubmit={this.onSubmit}>
											<h4>Forgot Password</h4>
											<div className="">
												<div className="input-field">
													<label id="username-lbl" for="username" className=" required">Email Address<span className="star">&#160;*</span></label>
													<input type="text" name="username" id="username" aria-required="true" value={this.state.email} onChange={this.onEmailChange} />
													{this.validator.message('email', this.state.email, 'required|email')}
												</div>
											</div>
											<div className="row">
											<div className="input-field col-md-6">
												<label id="password-lbl" for="password" className="required">Enter New Password<span className="star">&nbsp;*</span></label>
												<input type="password" name="password" id="password" aria-required="true" value={this.state.password} onChange={this.onChange} />
												{this.validator.message('password', this.state.password, 'required|min:6')}
											</div>
											<div className="input-field col-md-6">
												<label id="cpassword-lbl" for="cpassword" className="required">Confirm New Password<span className="star">&nbsp;*</span></label>
												<input type="password" name="confirmPassword" aria-required="true" id="cpassword" value={this.state.confirmPassword} onChange={this.onChange} />
												{this.validator.message('confirmPassword', this.state.confirmPassword, 'required|min:6')}
											</div>
											</div>
											<div className="row">
											<div className="input-field col-md-6">
												<div className="security-check">
													<div className="input-group-prepend">
														<span>{this.state.rand1} + {this.state.rand2}</span>
													</div>
													<div className="security-addon">
														<label id="security-lbl" for="security" className="required">Security Check <span className="star">&nbsp;*</span></label>
														<input type="number" name="securityVal" id="security" value={this.state.securityVal} onChange={this.onRecaptchaChange} />
														{this.validator.message('Recaptcha', this.state.securityVal, 'required')}
													</div>
												</div>
											</div>
											<div className="input-field col-md-6">
												{this.state.passwordStatus === false &&
													<div className="alert alert-danger alert-dismissible fade show resetpass-error" role="alert">
														<p>Password does not match</p>
													</div>}
													{this.state.security === false &&
													<div className="alert alert-danger alert-dismissible fade show resetpass-error" role="alert">
														<p>Incorrect Recaptcha</p>
													</div>}	
											</div>
											</div>
											<div className="">
												<div className="input-field s12">
													<button type="submit" className="btn btn-lg">Submit</button>
												</div>
											</div>
										</form>
										<br/>
										<div className="login-links">
											<div className="input-field">
												<Link to='/userlogin'>Back to Login.</Link>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</section>
				</main>
			</>
		);
	}
}

const mapStateToProps = (state) => ({
	alert: state.alert,
});
export default connect(mapStateToProps, { changeUserPassword, clearAllAlerts })(ForgotPassword);