import React,{Component} from 'react';
import AOS from 'aos';
import {Helmet} from "react-helmet";
import { connect } from 'react-redux';
import Header from '../../components/user/Header';
import ContactUsForm from '../../components/frontend/contactus/ContactUsForm';
import DharmaFooter from '../../components/frontend/common/DharmaFooter';

class Contactus extends Component{


	componentDidMount() {
        AOS.init();
    window.scrollTo(0, 0);
	}

	render(){
		return(
			<>
			<Helmet>
                <meta charSet="utf-8" />
                <title>Connect with us to learn more about Online Yoga Training.</title>
                <link rel="canonical" href="https://courses.sattvaconnect.com/user/contact-us" />
				<meta name="keywords" content="meditation lessons online, online yoga training," />
				<meta name="description" content="Indulge in the series of Online Yoga Training and the teachings of yogic practices from our yogies, straight from Rishikesh, the Yoga Capital of the world." />
            </Helmet>
			<Header  history={this.props.history}/>
			<ContactUsForm/>
			<DharmaFooter/>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {})(Contactus);