import React from 'react';

function AudioDetails(props) {
    let divClass = '';
    if (props.checkPreLaunch === true && props.audio.pre_launch === '0') {
        divClass = 'videos-intabs disableData';
    } else {
        divClass = 'videos-intabs';
    }
    return (
        <>
            <div className={divClass}>
                <div className="row">
                    <div className="col-md-4 col-lg-4">
                    <div className="imgvid-div">
                        <img src={props.audio.thumbnail} className="img-fluid image scale-on-hover moduleContentImage" />
                        <img src={require('../../../assets/images/play-button.png')} className="img-fluid moduleContentImage play_button_image" />
                        </div>
                        <div className='displayNonDiv'>
                        <audio className="mt-5" src={props.audio.audio} className='moduleContent' controls>
                            <p>Fallback content goes here.</p>
                        </audio>
                        </div>
                    </div>
                    <div className="col-md-8 col-lg-8">
                        <h5 className="mb-3">{props.audio.title}</h5>
                        <p>{props.audio.description}</p>
                    </div>
                </div>
            </div>
            <hr />
        </>
    );
}

export default AudioDetails;