import React,{Component} from 'react';
import { connect } from 'react-redux';
import {ToastsContainer, ToastsStore, ToastsContainerPosition} from 'react-toasts';
import {fetchCourse} from '../../../actions/courseAction';
import CourseListDetails from './CourseListDetails';
import { InfiniteScroll } from 'react-simple-infinite-scroll';

class CoursesList extends Component{

	componentWillMount(){
		this.props.fetchCourse('0', []);
	  }
	//   loadMore = () => {
	// 	this.props.fetchCourse(this.props.cursor, this.props.courses);
	//   } 

	render(){
		const { alert } = this.props;
		return(
			<>
			{alert.message && alert.type == 'success' &&
                  ToastsStore.success(alert.message) }
            {alert && alert.type == 'error' &&
                  ToastsStore.error('Something went wrong') } 
            <ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT}/>      
			<section className="sec most-courses">
		    	<div className="container">
		        	<h4>Featured</h4>
		        	<div className="row">
					{/* <InfiniteScroll
						throttle={100}
						threshold={300}
						isLoading={alert.isloading}
						hasMore={this.props.hasMore}
						onLoadMore={this.loadMore}
						>	 */}
		        	{this.props.courses.map((item,index)=>{
					return(
		        		<CourseListDetails key={index} course={item	}/>
						);
					})}	
					{/* </InfiniteScroll> */}
					</div>
				</div>
			</section>	
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  courses: state.courses.items,
  alert: state.alert,
  cursor:state.courses.cursor,
  hasMore:state.courses.hasMore,
});

export default connect(mapStateToProps, {fetchCourse})(CoursesList);