import React, { Component } from 'react';
import axios from 'axios';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { ToastsContainer, ToastsStore, ToastsContainerPosition } from 'react-toasts';
import CourseServices from '../../services/courseServices';
import SimpleReactValidator from 'simple-react-validator';
import { storeUserData } from '../../actions/courseAction';
import { setLoadingTrue, clearAllAlerts } from '../../actions/userAction';
import { getLocalStorageAuth, apiRoute, getApiHeader } from '../../utils/helpers';
import CountryOptions from '../frontend/common/CountryOptions';
import PhoneCodeOptions from '../frontend/common/PhoneCodeOptions';

class GiftCourseForm extends Component {

  constructor(props) {
    super(props);
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onEmailChange = this.onEmailChange.bind(this);
    this.getCurrentDate = this.getCurrentDate.bind(this);
    this.onCheckboxChange = this.onCheckboxChange.bind(this);
    this.onCountryChange = this.onCountryChange.bind(this);
    this.validator = new SimpleReactValidator();
    this.state = {
      firstName: '',
      lastName: '',
      email: '',
      country: '',
      countryCode: '',
      date: new Date(),
      terms: '',
      error: false,
      errorMessages: '',
      courseId: this.props.courseId,
      userId:'',
      giftedDate: this.getCurrentDate(),
      redirectToPayment: false,
      emailAvailability: true,
    }

  }

  getCurrentDate() {
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1; //January is 0!

    var yyyy = today.getFullYear();
    if (dd < 10) {
      dd = '0' + dd;
    }
    if (mm < 10) {
      mm = '0' + mm;
    }
    var today = yyyy + '-' + mm + '-' + dd;
    return today;
  }

  onChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  onCountryChange(e) {
    var index = e.target.selectedIndex;
    var optionElement = e.target.childNodes[index]
    var phoneCode = optionElement.getAttribute('phoneCode');
    this.setState({ country: e.target.value });
    this.setState({ countryCode: phoneCode });
  }

  onCheckboxChange(e) {
    this.setState({ terms: e.target.checked });
  }

  onEmailChange(e) {
    const email = e.target.value;
    this.setState({ email: email });
    if (typeof email !== "undefined") {
      let lastAtPos = email.lastIndexOf('@');
      let lastDotPos = email.lastIndexOf('.');

      if ((lastAtPos < lastDotPos && lastAtPos > 0 && email.indexOf('@@') == -1 && lastDotPos > 2 && (email.length - lastDotPos) > 2)) {
        CourseServices.checkEmailAvailability(email)
          .then(res => {
            if (res.data.status !== true) {
              this.setState({ emailAvailability: false });
            } else {
              this.setState({ emailAvailability: true });
            }
          })
      } else {
        this.setState({ emailAvailability: true });
      }
    } else {
      this.setState({ emailAvailability: true });
    }
  }


  onSubmit(e) {
    e.preventDefault();
    if (this.state.emailAvailability == false) {
      return false;
    }
    if (!this.validator.allValid()) {
      this.validator.showMessages();
      this.forceUpdate();
      return false;
    }
    const auth = getLocalStorageAuth();
    const userData = {
      fullname: auth.userDetails.first_name,
      buyerEmail: auth.userDetails.email,
      buyerCountry: auth.userDetails.country,
      buyerCountryCode: auth.userDetails.country_code,
      buyerPhone: auth.userDetails.phone,
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      email: this.state.email,
      country: this.state.country,
      countryCode: this.state.countryCode,
      giftedDate: this.state.giftedDate,
      userType: 2,
      courseId: this.state.courseId,
    }
    this.props.setLoadingTrue();
    const requestOptions = {
      headers: getApiHeader(true)
    };
    axios.post(apiRoute('gift-user-registration'), userData, requestOptions)
      .then(res => {
        this.setState({
          redirectToPayment: true,
          error: false,
          errorMessages: '',
          redirect: true,
          userId:res.data.id,
        })
        this.props.clearAllAlerts();
      })
      .catch(error => {
        if (error.response && error.response.status) {
          let errorData = error.response.data;
          this.setState({
            error: true,
            errorMessages: errorData.message,
          })
        } else {
          this.setState({
            error: true,
            errorMessages: 'Something Went Wrong, Try Again',
          })
        }
        this.props.clearAllAlerts();
      });

  }

  render() {

    const handleOnBlur = ({ target: { value } }) => {
      this.setState({ giftedDate: value })
    }
    return (
      <>
        {this.state.redirectToPayment &&
          <Redirect to={'/payment/'+this.state.courseId+'/'+btoa(this.state.userId)} />
        }
        <ToastsContainer store={ToastsStore} position={ToastsContainerPosition.TOP_RIGHT} />
        <form onSubmit={this.onSubmit} id='giftCourseForm' autocomplete="off" className="form form-horizontal">

          <div className="card p-4 d-block">
            <div className="customer-support p-0">
              <h4>Receiver Details</h4>
              {this.state.error ?
                <div className="col-sm-6">
                  <div className="alert alert-danger alert-dismissible fade show" role="alert">
                    <p>{this.state.errorMessages}.</p>
                  </div></div> : null}
              {this.state.emailAvailability == false &&
                <div className="col-sm-6">
                  <div className="alert alert-danger alert-dismissible fade show" role="alert">
                    <p>Email is already in used.</p>
                  </div></div>}
              <div className="row detail-form">
                <div className="input-field col-md-6">
                  <label id="firstName-lbl" for="firstName" className=" required">Receiver's First Name<span className="star">&#160;*</span></label>
                  <input type="text" name="firstName" id="firstName" value={this.state.firstName} onChange={this.onChange} />
                  {this.validator.message('firstName', this.state.firstName, 'required|alpha|max:50')}
                </div>
                <div className="input-field col-md-6">
                  <label id="lastName-lbl" for="lastName" className="required">Receiver's Last Name<span className="star">&#160;*</span></label>
                  <input type="text" name="lastName" id="lastName" value={this.state.lastName} onChange={this.onChange} />
                  {this.validator.message('lastName', this.state.lastName, 'required|alpha|max:50')}
                </div>
                <div className="input-field col-md-6">
                  <label id="email-lbl" for="email" className=" required">Receiver's Email<span className="star">&#160;*</span></label>
                  <input type="text" name="email" id="buyerEmail" value={this.state.email} onChange={this.onEmailChange} />
                  {this.validator.message('email', this.state.email, 'required|email')}
                </div>
                <div className="input-field col-md-6">
                  <label id="date-lbl" for="date" className="required active">Select Send Date<span className="star">&nbsp;*</span></label>
                  <DatePicker
                    selected={this.state.date}
                    onChange={date => this.setState({ date: date })}
                    onBlur={handleOnBlur}
                    minDate={new Date()}
                    dateFormat="yyyy-MM-dd"
                  />
                </div>
                <div className="input-field col-md-6">
                  <select id="country" name="country" value={this.state.country} onChange={this.onCountryChange}>
                    <option selected>Receiver's Country</option>
                    <CountryOptions />
                  </select>
                  {this.validator.message('country', this.state.country, 'required')}
                </div>
                <div className="input-field col-md-6">
                  <select id="countryCode" name="countryCode" value={this.state.countryCode} onChange={this.onChange}>
                    <option>Receiver's Dial Code(+)</option>
                    <PhoneCodeOptions />
                  </select>
                  {this.validator.message('countryCode', this.state.countryCode, 'required')}
                </div>
                <div className="">
                  <div className="">
                    <div className="checkbox">
                      <input id="terms" type="checkbox" name="terms" value="terms" onChange={this.onCheckboxChange} checked={this.state.terms} />
                      {this.validator.message('terms', this.state.terms, 'required')}
                      <label for="terms">I have accept the <a href="/terms-of-services" target="_blank"> Terms and Conditions</a></label>
                    </div>
                  </div>
                </div>
              </div>
              <div className="input-field text-right">
                <button type="submit" className="btn btn-lg">Proceed to Payment</button>
              </div>
            </div>
          </div>
        </form>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  course: state.courses.item,
});
export default connect(mapStateToProps, { storeUserData, setLoadingTrue, clearAllAlerts })(GiftCourseForm);