import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import cookie from 'react-cookies';
import { apiRoute, getApiHeader,setLocalStorageAuth } from '../../../utils/helpers';
import { setLoadingTrue, clearAllAlerts } from '../../../actions/userAction';
import CourseServices from '../../../services/courseServices';
import SimpleReactValidator from 'simple-react-validator';

class QuickCourseTopForm extends Component {

    constructor(props) {
        super(props);
        this.validator = new SimpleReactValidator();
        this.state = {
            fullName: '',
            lastName: '',
            email: '',
            address: '',
            affiliateUrlId: this.props.affiliateUrlId,
            courseId: this.props.courseId,
            emailAvailability: true,
            error:false,
            errorMessages:'',
            redirect:false,
            redirectToLogin:false,
        }
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onEmailChange = this.onEmailChange.bind(this);
    }

    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onEmailChange(e) {
        const email = e.target.value;
        this.setState({ email: email });
        if(typeof email !== "undefined"){
            let lastAtPos = email.lastIndexOf('@');
            let lastDotPos = email.lastIndexOf('.');
 
            if ((lastAtPos < lastDotPos && lastAtPos > 0 && email.indexOf('@@') == -1 && lastDotPos > 2 && (email.length - lastDotPos) > 2)) {
                CourseServices.checkEmailAvailability(email)
                .then(res => {
                    if (res.data.status !== true) {
                        this.setState({ emailAvailability: false });
                    } else {
                        this.setState({ emailAvailability: true });
                    }
                })
             }else{
                this.setState({ emailAvailability: true });
             }
        }else{
            this.setState({ emailAvailability: true });
        } 
    }

    onSubmit(e) {
        e.preventDefault();
        if (!this.validator.allValid()) {
            this.validator.showMessages();
            this.forceUpdate();
            return false;
        }
        this.props.modalref.current.click();
        if (this.state.emailAvailability === false) {
            this.setState({ redirectToLogin:true  });
            return false;
        }
        this.props.modalref.current.click();
        const userData = {
            fullName: this.state.fullName,
            email: this.state.email,
            courseId: this.state.courseId,
            affiliateUrlId: this.state.affiliateUrlId,
        }
        this.props.setLoadingTrue();
        const requestOptions = {
            headers: getApiHeader()
        };
        axios.post(apiRoute('new-course-user-registration'), userData, requestOptions)
            .then(res => {
                this.setState({
                    redirectToPayment: true,
                    error:false,
                    errorMessages: '',
                    redirect:true,
                })
                cookie.save('username', res.data.userDetails.username, { path: '/userlogin' });
                cookie.save('password', res.data.password, { path: '/userlogin' })
                setLocalStorageAuth(res.data);
                this.props.clearAllAlerts();

                const mailData = {
                    userDetails : res.data.userDetails,
                    password : res.data.password,
                }
                axios.post('https://www.sattvaconnect.com/backendportal/api/send-registration-mail', mailData, requestOptions)  
            })
            .catch(error => {
                if (error.response && error.response.status) {
                    let errorData = error.response.data;
                    this.setState({
                        error:true,
                        errorMessages: errorData.message,
                    })
                } else {
                    this.setState({
                        error:true,
                        errorMessages: 'Something Went Wrong, Try Again',
                    })
                }
                this.props.clearAllAlerts();
            });

    }

    render() {
        {this.state.redirect &&
            this.props.history.push({
                pathname: '/registration_success',
                state: { isValid:true}
            }
           )
        }
        {this.state.redirectToLogin &&
            this.props.history.push({
                pathname: '/userlogin',
                state: { showMsg:true}
            }
           )
        }
        return (
            <>
           
                <form onSubmit={this.onSubmit} id='buyCourseForm' autoComplete="off">
                    {this.state.error ?
                            <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                <p>{this.state.errorMessages}.</p>
                            </div> :null}
                    {/* {this.state.emailAvailability === false &&
                            <div className="alert alert-danger alert-dismissible fade show" role="alert">
                                <p>This Email has already Registered.You can Login with the Temp User ID, Password, and Link we Sent you.<br/>Search your Inbox and Spam for Sattva Connect to find that email</p>
                            </div>} */}
                            <div className="form-group mb-0">
                                <input type="text" name="fullName" placeholder="" className="form-control mb-3" id="fullName"  placeholder="Full Name" value={this.state.fullName} onChange={this.onChange} />
                                {this.validator.message('fullName', this.state.fullName, 'required|max:50')}
                             <input type="text" name="email" placeholder="" className="form-control" id="email" placeholder="Email" value={this.state.email} onChange={this.onEmailChange} />
                            {this.validator.message('email', this.state.email, 'required|email')}
                            </div>
                         <button type="button" type="submit" className="btn formBtn ml-3">Join</button>
                          
                </form>
            </>
        );
    }
}

const mapStateToProps = (state) => ({
    course: state.courses.item,
});
export default connect(mapStateToProps, { setLoadingTrue, clearAllAlerts })(QuickCourseTopForm);