import React,{Component} from 'react';
import axios from 'axios';
import AOS from 'aos';
import {Helmet} from "react-helmet";
import { apiRoute, getApiHeader } from '../../utils/helpers';
import RoadToDharmaHeader from '../../components/frontend/common/RoadToDharmaHeader';
import Footer from '../../components/frontend/common/Footer';
import Banner from '../../components/frontend/common/Banner';
import TextSection from '../../components/frontend/common/TextSection';
import CoursesList from '../../components/frontend/course/CoursesList';

class Courses extends Component{

	constructor(props){
    	super(props);
	      this.state = {
	      	banner : {},
		    } 
		  }


	componentDidMount() {
	AOS.init();
   	 window.scrollTo(0, 0);

   	 const requestOptions = {
			headers : getApiHeader()
		};
		axios.get(apiRoute('cms-page-banner/'+btoa(3)),requestOptions)
		.then(res =>{
			this.setState({ banner : res.data });
	    })

	}

	render(){
		return(
			<>
			<Helmet>
                <meta charSet="utf-8" />
                <title>Online Courses on Yogic Teaches and Practices</title>
                <link rel="canonical" href="http://courses.sattvaconnect.com" />
				<meta name="keywords" content="Online yoga courses, Online meditation courses" />
				<meta name="description" content="Sattva Connect offers you Online Courses to deepen your knowlegde into the Authentic Teachings and Full-Spectrum Practices of Yoga. " />
            </Helmet>
			<RoadToDharmaHeader/>
			<Banner banner={this.state.banner}/>
			<main>
			<TextSection sectionClass="sec sec-inabout pb-0">
				<p>Welcome to Sattva Connect Courses. Here is an opportunity for you to deepen your knowledge into the yogic teachings and practices. The conscious person is always committed to learning. Watch incredible videos filmed in India in the Himalayas. There will be additional materials in each course to further enhance your growing understanding.</p>
		        <p>More courses are already being developed. You will see them soon!</p>
		        <p>May you be inspired!</p>
			</TextSection>
			<CoursesList/>
			</main>
			<Footer/>
			</>
			);
	}
}

export default Courses;