import React,{Component} from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { apiRoute, getApiHeader } from '../../utils/helpers';
import Header from '../../components/user/Header';
import Banner from '../../components/frontend/common/Banner';
import MyCoursesList from '../../components/user/MyCoursesList';
class MyCourses extends Component{

	constructor(props){
    	super(props);
	      this.state = {
	      	banner : {},
		    } 
		  }


	componentDidMount() {

   	 window.scrollTo(0, 0);

   	 const requestOptions = {
			headers : getApiHeader()
		};
		axios.get(apiRoute('cms-page-banner/'+btoa(14)),requestOptions)
		.then(res =>{
			this.setState({ banner : res.data });
	    })
	}

	render(){
		return(
			<>
			<Header  history={this.props.history}/>
			  <main className="admin-content">
    			<Banner banner={this.state.banner}/>
		    	<MyCoursesList history={this.props.history}/>
			  </main>
			</>
			);
	}
}

const mapStateToProps = (state) =>({
  alert: state.alert,
});
export default connect(mapStateToProps, {})(MyCourses);